import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { api } from "../axios/api";

//  Login
export const loginUser = createAsyncThunk(
  "user/login",
  async ({ email, senha }, { rejectWithValue }) => {
    try {
      const response = await api.post("/Cliente.php", {
        action: "login",
        email,
        senha,
      });
      return response.data;
    } catch (error) {
      return rejectWithValue(
        error.response?.data?.message || "Erro no login"
      );
    }
  }
);

//  Restaurar user do localStorage
export const checkAuth = createAsyncThunk(
  "user/checkAuth",
  async (_, { rejectWithValue }) => {
    try {
      const user = JSON.parse(localStorage.getItem("user"));
      if (user) {
        return { data: user };
      }
      return rejectWithValue("No autenticado");
    } catch (err) {
      return rejectWithValue("Erro ao verificar autenticao");
    }
  }
);

//  Logout
export const logout = createAsyncThunk(
  "user/logout",
  async (userId, { rejectWithValue }) => {
    try {
      await api.get(`/Cliente.php?action=logout&id=${userId}`);
      localStorage.removeItem("user");
      return true;
    } catch (error) {
      return rejectWithValue(
        error.response?.data?.message || "Erro no logout"
      );
    }
  }
);

const initialState = {
  user: JSON.parse(localStorage.getItem("user")) || null,
  darkMode: localStorage.getItem("darkMode") === "false",
  loading: false,
  error: null,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    setUser: (state, action) => {
      state.user = action.payload.data;
      localStorage.setItem("user", JSON.stringify(action.payload.data));
    },
    toggleDarkMode: (state) => {
      state.darkMode = !state.darkMode;
      localStorage.setItem("darkMode", state.darkMode);
    },
  },
  extraReducers: (builder) => {
    builder
      // LOGIN
      .addCase(loginUser.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(loginUser.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload.data;
        localStorage.setItem("user", JSON.stringify(action.payload.data));
      })
      .addCase(loginUser.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })

      // CHECK AUTH
      .addCase(checkAuth.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(checkAuth.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload.data;
      })
      .addCase(checkAuth.rejected, (state) => {
        state.loading = false;
        state.user = null;
      })

      // LOGOUT
      .addCase(logout.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(logout.fulfilled, (state) => {
        state.loading = false;
        state.user = null;
      })
      .addCase(logout.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { setUser, toggleDarkMode } = userSlice.actions;
export default userSlice.reducer;
