import { Link, useNavigate } from 'react-router-dom';
import { error404, Logo2 } from '../static';

const NotFound = () => {
  const navigate = useNavigate();

  const handleGoHome = () => {
    navigate(-1);
  };

  return (
    <div className="bg-light min-vh-100 d-flex flex-column align-items-center justify-content-center text-center">
      <div className="container">
        <div className="row justify-content-center align-items-center">
          <div className="col-xl-8">
            <div className="py-10 py-md-10">
              <img 
                src={error404} 
                alt="Erro 404" 
                width = {250}
                className="img-fluid mb-10" 
              />
              <h1 className="mb-4 fs-55 text-primary">Ops, algo deu errado!</h1>
              <p className="max-w-600px mx-auto fs-15 text-muted">
                A pgina que voc est procurando no existe ou foi movida. Verifique o endereo ou use o boto abaixo para voltar para a pgina principal.
              </p>
              
              <div className="mt-8 d-flex justify-content-center gap-2">
                <button
                  onClick={handleGoHome}
                  className="btn btn-primary rounded-pill px-5"
                >
                  Voltar ao Incio
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NotFound;