import React, { useEffect, useState, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import "animate.css";
// Importar SweetAlert2
import Swal from "sweetalert2"; 
import { hero1 } from "../static";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { numberFormat } from '../config/Config';
import { useSelector } from "react-redux";
import Title from "../components/Title";

const DetalhesProduto = () => {
    const { id } = useParams();

    const [produto, setProduto] = useState(null);
    const [relacionados, setRelacionados] = useState([]);
    const [propriedades, setPropriedades] = useState([]);
    const [fotos, setFotos] = useState([]);

    const [loadingProduto, setLoadingProduto] = useState(true);
    const [loadingRelacionados, setLoadingRelacionados] = useState(true);
    const [loadingPropriedades, setLoadingPropriedades] = useState(true);
    const [loadingFotos, setLoadingFotos] = useState(true);
    const [isAddingToCart, setIsAddingToCart] = useState(false);
    
    const [quantidade, setQuantidade] = useState(1);
    const [fotoSelecionada, setFotoSelecionada] = useState(null);
    const { user } = useSelector((state) => state.user);

    /**
     *  ENDPOINT PARA ADICIONAR PRODUTOS AO CARRINHO (createOrUpdate)
     */
    const handleAddToCart = async () => {
        if (!produto) return;

        if (!user || !user.id) {
            // Manter o alerta original para o login
            alert(" Por favor, faa login para adicionar produtos ao carrinho.");
            return;
        }

        setIsAddingToCart(true);

        const dataToSend = {
            action: 'createOrUpdate',
            cliente_id: user.id,
            produto_id: produto.id,
            qtd: quantidade,
        };

        try {
            const res = await api.post('/Carrinho.php', dataToSend);
            
            if (res.data.success) {
                //  Lgica para o SweetAlert2 (swal) no SUCESSO
                let timerInterval;
                await Swal.fire({
                    title: ` Produto Adicionado!`,
                    html: `"${produto.nome}" foi adicionado ao seu carrinho.
                           <br/>A pgina ser recarregada em **<b class="text-danger">3</b>** segundos.`,
                    icon: 'success',
                    timer: 3000, // 3 segundos
                    timerProgressBar: true,
                    allowOutsideClick: false, // No permite fechar clicando fora
                    backdrop: false, // Remove o backdrop escuro
                    willOpen: (toast) => {
                        toast.addEventListener('mouseenter', Swal.stopTimer);
                        toast.addEventListener('mouseleave', Swal.resumeTimer);
                        const b = Swal.getHtmlContainer().querySelector('b');
                        timerInterval = setInterval(() => {
                            // Atualiza o contador exibido
                            const timeLeft = Math.ceil(Swal.getTimerLeft() / 1000);
                            b.textContent = timeLeft;
                        }, 100);
                    },
                    didClose: () => {
                        clearInterval(timerInterval);
                        //  Executa o location.reload() aps o contador
                        window.location.reload(); 
                    }
                });

            } else {
                // Alerta para Erro de Negcio
                Swal.fire(' Erro', res.data.message || "No foi possvel adicionar o produto.", 'error');
            }
        } catch (error) {
            console.error("Erro na requisio do carrinho:", error);
            // Alerta para Erro de Conexo/Servidor
            Swal.fire(' Erro de Conexo', "Ocorreu um erro ao comunicar com o servidor.", 'error');
        } finally {
            setIsAddingToCart(false);
            // Note: O reload est dentro do didClose do Swal.fire(success)
        }
    };

    // ... (O resto do seu cdigo permanece inalterado)

    //  Buscar produtos relacionados
    const getProdutosRelacionados = useCallback(async (subcategoriaId, currentProductId) => {
        setLoadingRelacionados(true);
        try {
            const res = await api.get(`/Produto.php?action=listBySubcategoria&subcategoria_id=${subcategoriaId}`);
            const filtered = (res.data.data || [])
                .filter(item => item.id.toString() !== currentProductId.toString())
                .slice(0, 4);
            setRelacionados(filtered);
        } catch (error){
            console.error("Erro ao buscar produtos relacionados:", error);
            setRelacionados([]);
        } finally {
            setLoadingRelacionados(false);
        }
    }, []);

    //  Buscar propriedades
    const getPropriedadesProduto = useCallback(async (produtoId) => {
        setLoadingPropriedades(true);
        try {
            const res = await api.get(`/PropriedadesProduto.php?action=listByProduto&produto_id=${produtoId}`);
            setPropriedades(res.data?.data || []);
        } catch {
            setPropriedades([]);
        } finally {
            setLoadingPropriedades(false);
        }
    }, []);

    //  Buscar fotos adicionais
    const getFotosProduto = useCallback(async (produtoId, fotoPrincipal) => {
        setLoadingFotos(true);
        try {
            const res = await api.get(`/FotosProduto.php?action=listByProduto&produto_id=${produtoId}`);
            const fotosServidor = res.data?.data || [];

            //  Sempre incluir a foto principal na frente
            const listaFinal = [
                { foto: fotoPrincipal },
                ...fotosServidor.filter(f => f.foto !== fotoPrincipal)
            ];

            setFotos(listaFinal);
            setFotoSelecionada(url + "/uploads/" + fotoPrincipal);
        } catch (error) {
            console.error("Erro ao buscar fotos:", error);
            setFotos([{ foto: fotoPrincipal }]);
            setFotoSelecionada(url + "/uploads/" + fotoPrincipal);
        } finally {
            setLoadingFotos(false);
        }
    }, []);
    

    //  Buscar detalhes do produto
    const getProdutoDetails = useCallback(async () => {
        if (!id) return;
        setLoadingProduto(true);
        try {
            const res = await api.get(`/Produto.php?action=read&id=${id}`);
            const data = res.data.data;
            if (data) {
                setProduto(data);
                getPropriedadesProduto(data.id);
                getFotosProduto(data.id, data.foto);

                if (data.subcategoria_id) {
                    getProdutosRelacionados(data.subcategoria_id, data.id);
                }
            }
        } catch (error) {
            console.error("Erro ao buscar produto:", error);
            setProduto(null);
        } finally {
            setLoadingProduto(false);
        }
    }, [id, getProdutosRelacionados, getPropriedadesProduto, getFotosProduto]);

    useEffect(() => {
        getProdutoDetails();
        setQuantidade(1);
        window.scrollTo(0, 0);
    }, [getProdutoDetails]);

    const Loader = ({ message = "A carregar..." }) => (
        <div className="text-center py-5">
            <div className="spinner-border text-danger" role="status">
                <span className="visually-hidden">A carregar...</span>
            </div>
            <p className="mt-3 text-muted">{message}</p>
        </div>
    );

    return (
        <div className="d-flex flex-column" style={{position:"fixed", top:0, left:0, right:0, bottom:0, backgroundColor:"#f4f4f4"}}>
            <Title title="Detalhes do Produto" />
            <Navbar/>
            <main className="flex-grow-1 w-100" style={{overflowY:"auto", backgroundColor:"#fff"}}>

                <section className="p-4 pt-4 pb-5">
                    <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
                        {loadingProduto ? ' Detalhes do Produto' : produto ? produto.nome : 'Produto No Encontrado'}
                    </h3>

                    {loadingProduto && <Loader message="A carregar detalhes do produto..." />}

                    {produto && (
                        <div className="row animate__animated animate__fadeIn">
                            <div className="col-12 col-md-6 mb-4 text-center">
                                {fotoSelecionada && (
                                    <img
                                        src={fotoSelecionada}
                                        className="w-100 object-fit-cover rounded-3 shadow-sm"
                                        style={{ height:"30em", objectFit:"cover" }}
                                        alt={produto.nome}
                                    />
                                )}

                                {/*  MINIATURAS */}
                                {!loadingFotos && fotos.length > 0 && (
                                    <div className="d-flex justify-content-center mt-3 flex-wrap gap-2">
                                        {fotos.map((foto, i) => (
                                            <img
                                                key={i}
                                                src={url + "/uploads/" + foto.foto}
                                                alt="foto produto"
                                                className={`rounded shadow-sm ${fotoSelecionada === url + "/uploads/" + foto.foto ? "border border-danger border-3" : ""}`}
                                                style={{
                                                    width: "5em",
                                                    height: "5em",
                                                    objectFit: "cover",
                                                    cursor: "pointer"
                                                }}
                                                onClick={() => setFotoSelecionada(url + "/uploads/" + foto.foto)}
                                            />
                                        ))}
                                    </div>
                                )}
                            </div>

                            <div className="col-12 col-md-6 mb-4">
                                <h2 className="fw-bolder text-primary mb-3">{produto.nome}</h2>
                                <p className="text-secondary lead">{produto.descricao}</p>

                                <div className="my-4 border-top pt-3">
                                    <span className="fs-4 fw-bold text-dark">
                                        EUR {numberFormat(produto.preco, 2, ',', '.')}
                                    </span>
                                </div>

                                <div className="d-flex align-items-center mb-4">
                                    <button className="btn btn-outline-secondary" onClick={() => setQuantidade(q => Math.max(1, q - 1))}>
                                        <i className="fas fa-minus"></i>
                                    </button>
                                    <span className="mx-3 fs-5 fw-bold">{quantidade}</span>
                                    <button className="btn btn-outline-secondary" onClick={() => setQuantidade(q => q + 1)}>
                                        <i className="fas fa-plus"></i>
                                    </button>
                                </div>

                                <button 
                                    className="btn btn-danger btn-lg w-100 shadow-sm" 
                                    onClick={handleAddToCart}
                                    disabled={isAddingToCart}
                                >
                                    {isAddingToCart ? (
                                        <>
                                            <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                                            A Adicionar...
                                        </>
                                    ) : (
                                        <>
                                            <i className="fas fa-cart-plus me-2"></i> Adicionar ao Carrinho
                                        </>
                                    )}
                                </button>
                                {/* Mensagem de aviso se o usurio no estiver logado */}
                                {!user?.id && (
                                    <p className="text-danger small mt-2"> necessrio fazer login para adicionar ao carrinho.</p>
                                )}
                            </div>
                        </div>
                    )}
                </section>

                {/*  PROPRIEDADES */}
                {produto && (
                    <section className="p-4 pt-2 pb-5">
                        <h4 className="text-dark mb-3 fw-bold border-bottom pb-2"> Propriedades do Produto</h4>
                        {loadingPropriedades ? (
                            <Loader message="A carregar propriedades..." />
                        ) : propriedades.length > 0 ? (
                            <div className="table-responsive">
                                <table className="table table-bordered table-striped">
                                    <thead className="table-light">
                                        <tr><th>Propriedade</th><th>Valor</th></tr>
                                    </thead>
                                    <tbody>
                                        {propriedades.map((prop, i) => (
                                            <tr key={i}>
                                                <td className="fw-semibold">{prop.propriedade}</td>
                                                <td>{prop.valor}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        ) : (
                            <p className="text-muted">Nenhuma propriedade definida.</p>
                        )}
                    </section>
                )}

                {/*  HERO PARALLAX SECTION  */}
                <div 
                    className="d-flex align-items-center justify-content-center w-100"
                    style={{ 
                        height: "10em", 
                        backgroundImage: `url(${hero1})`,
                        backgroundPosition: 'center', 
                        backgroundSize: 'cover', 
                        backgroundAttachment: 'fixed', 
                        transition: 'background-position 0.5s ease',
                    }}
                >
                </div>
                {/* FIM DO HERO PARALLAX */}

                {/* 2. SEO DE PRODUTOS RELACIONADOS */}
                <section className="p-4 pt-5 pb-5">
                    <h3 className="text-dark mb-4 fw-bold border-bottom pb-2"> Produtos Relacionados</h3>
                    <div className="row">
                        {
                            loadingRelacionados ? (
                                <div className="col-12"><Loader message="A buscar produtos relacionados..." /></div>
                            ) : relacionados.length > 0 ? (
                                relacionados.map(item => (
                                    <div key={item.id} className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5">
                                        {/* Card de Produto Relacionado */}
                                        <Link 
                                            to={`/produtos/detalhes/${item.id}/`} 
                                            className="text-decoration-none text-dark"
                                            onClick={() => window.scrollTo(0, 0)} 
                                        >
                                            <img
                                                src = {`${url}/uploads/${item.foto}`}
                                                className="w-100 object-fit-cover rounded-3 shadow-sm"
                                                style = {{ height:"18em" }}
                                                alt={item.nome}
                                            />
                                            <h5 className="mb-0 mt-2 fw-semibold text-truncate">{item.nome}</h5>
                                            <b className="text-danger fs-5">
                                                {numberFormat(item.preco,2,',','.')}
                                            </b>
                                        </Link>
                                        
                                        <div
                                            className="position-absolute d-flex align-items-center"
                                            style={{ top: '1em', left: '1em' }}
                                        >
                                            <span
                                                style={{ background: "rgba(0,0,0,0.6)", backdropFilter: "blur(3px)" }}
                                                className="fw-bold p-1 px-2 rounded-4 text-white small animate__animated animate__fadeInUp"
                                            >
                                                Relacionado
                                            </span>
                                        </div>
                                    </div>
                                ))
                            ) : (
                                <div className="col-12 text-center text-muted py-5">
                                    <i className="fas fa-search-minus fa-3x mb-3"></i>
                                    <p>Nenhum produto relacionado encontrado para esta categoria.</p>
                                </div>
                            )
                        }
                    </div>
                </section>

                <Footer/>
            </main>
        </div>
    );
};

export default DetalhesProduto;