import { useEffect, useState } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api"; 
import { Link } from "react-router-dom";
//  Adicionado: Importar Modal e Button do React-Bootstrap
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import {useSelector} from 'react-redux'
import Title from "../components/Title";

// Funo auxiliar para formatar preo (adaptada)
const formatCurrency = (value) => {
    // Garante que o valor seja um nmero antes de formatar
    const num = parseFloat(value) || 0; 
    return num.toFixed(2).replace('.', ',') + ' ';
};

// Funo para obter a classe de cor do status (Mantida)
const getStatusColor = (status) => {
    switch (status) {
        case 'Entregue':
            return 'text-success border-success';
        case 'Confirmado':
            return 'text-warning border-warning';
        case 'Novo':
            return 'text-primary border-primary';
        case 'Cancelado':
            return 'text-danger border-danger';
        default:
            return 'text-muted border-muted';
    }
};


// ===================================================================
//  COMPONENTE DE DETALHES DO PEDIDO (MODAL)
// ===================================================================
const EncomendaDetailsModal = ({ show, handleClose, encomenda }) => {
    if (!encomenda) return null;
    
    // Calcula o total geral
    const subtotal = parseFloat(encomenda.subtotal) || 0;
    const taxa_entrega = parseFloat(encomenda.taxa_entrega) || 0;
    const totalGeral = subtotal + taxa_entrega;

    const dateObj = new Date(encomenda.createdAt);
    const date = dateObj.toLocaleDateString('pt-PT');
    const time = dateObj.toLocaleTimeString('pt-PT', { hour: '2-digit', minute: '2-digit' });

    // Simulao da estrutura de itens da encomenda
    const itens = encomenda.itens || [];

    return (
        <Modal show={show} onHide={handleClose} size="lg" centered>
            <Modal.Header closeButton className={`bg-light ${getStatusColor(encomenda.status)}`}>
                <Modal.Title className="fw-bold">
                    Detalhes do Pedido #**{encomenda.id}**
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="row">
                    {/* Resumo e Status */}
                    <div className="col-md-6 mb-4">
                        <h5 className="border-bottom pb-2 mb-3">Resumo</h5>
                        <p><strong>Estado:</strong> <span className={`fw-bold ${getStatusColor(encomenda.status)}`}>{encomenda.status}</span></p>
                        <p><strong>Data/Hora:</strong> {date} s {time}</p>
                        <p><strong>Total de Itens:</strong> {encomenda.total_items || itens.length}</p>
                    </div>

                    {/* Endereo e Cliente (Simulao - assumindo que os dados esto na encomenda) */}
                    <div className="col-md-6 mb-4">
                        <h5 className="border-bottom pb-2 mb-3">Detalhes de Entrega</h5>
                        <p><strong>Cliente:</strong> {encomenda.cliente_nome || 'N/A'}</p>
                        <p><strong>Telefone:</strong> {encomenda.cliente_telefone || 'N/A'}</p>
                        <p><strong>Endereo:</strong> {encomenda.morada || encomenda.endereco || 'N/A'}</p>
                    </div>
                </div>

                {/* Tabela de Itens */}
                <h5 className="border-bottom pb-2 mb-3 mt-3">Itens do Pedido</h5>
                {itens.length > 0 ? (
                    <div className="table-responsive">
                        <table className="table table-striped table-sm align-middle">
                            <thead className="table-dark">
                                <tr>
                                    <th>Produto</th>
                                    <th className="text-center">Qtd</th>
                                    <th className="text-end">Preo Unitrio</th>
                                    <th className="text-end">Subtotal</th>
                                </tr>
                            </thead>
                            <tbody>
                                {itens.map((item, index) => (
                                    <tr key={index}>
                                        <td>
                                            {item.produto_nome}
                                            {item.opcoes && item.opcoes.length > 0 && 
                                                <small className="d-block text-muted">({item.opcoes.join(', ')})</small>
                                            }
                                        </td>
                                        <td className="text-center">{item.qtd}</td>
                                        <td className="text-end">{formatCurrency(item.preco)}</td>
                                        <td className="text-end fw-bold">{formatCurrency(item.qtd * item.preco)}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                ) : (
                    <div className="alert alert-warning text-center">Nenhum item detalhado disponvel.</div>
                )}
                
                {/* Rodap de Totais */}
                <div className="text-end mt-4">
                    <h4 className="fw-bolder text-success">Total Geral: {formatCurrency(totalGeral)}</h4>
                </div>

            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Fechar
                </Button>
            </Modal.Footer>
        </Modal>
    );
};

// ===================================================================
//  COMPONENTE PRINCIPAL (Encomend)
// ===================================================================
const Encomend = () => {
    // -------------------------------------------------------------------
    //  ESTADOS DO COMPONENTE
    // -------------------------------------------------------------------
    const [encomendas, setEncomendas] = useState([]);
    const [loadingEncomendas, setLoadingEncomendas] = useState(true);
    const user = useSelector(state => state.user.user)
    //  NOVOS ESTADOS PARA O MODAL
    const [showDetailsModal, setShowDetailsModal] = useState(false);
    const [selectedEncomenda, setSelectedEncomenda] = useState(null);

    // ID do cliente fixo para simulao (Em produo, deve vir do contexto/sesso)
    const CLIENTE_ID = user.id; 

    // -------------------------------------------------------------------
    //  HANDLERS DO MODAL
    // -------------------------------------------------------------------

    const handleOpenDetails = (encomenda) => {
        setSelectedEncomenda(encomenda);
        setShowDetailsModal(true);
    };

    const handleCloseDetails = () => {
        setShowDetailsModal(false);
        setSelectedEncomenda(null);
    };


    // -------------------------------------------------------------------
    //  FETCH DE DADOS (Mantido, mas adaptado para incluir itens)
    // -------------------------------------------------------------------
    const getEncomendas = async () => {
        setLoadingEncomendas(true);
        try {
            // Buscamos os dados da encomenda. Para o Modal funcionar, a API deve retornar:
            // - Dados da Encomenda (id, status, subtotal, taxa_entrega, cliente_nome, morada, etc.)
            // - Um array 'itens' dentro de cada encomenda com: (nome_produto, quantidade, preco_unitario, etc.)
            const res = await api.get(`/Encomenda.php?action=listByCliente&cliente_id=${CLIENTE_ID}`);
            
            // Supondo que a estrutura da API agora inclua os detalhes dos itens
            const dataWithItems = res.data.data.map(e => ({
                ...e,
            }));

            setEncomendas(dataWithItems); 
        } catch (error) {
            console.error("Erro ao buscar encomendas:", error);
            setEncomendas([]);
        } finally {
            setLoadingEncomendas(false);
        }
    };

    useEffect(() => {
        getEncomendas();
    }, []); 

    // -------------------------------------------------------------------
    //  RENDERIZAO DE COMPONENTES
    // -------------------------------------------------------------------
    const Loader = () => (
        <div className="text-center py-5">
            <div className="spinner-border text-primary" role="status">
                <span className="visually-hidden">A carregar...</span>
            </div>
            <p className="mt-3 text-muted">A carregar suas encomendas...</p>
        </div>
    );

    const EncomendaCard = ({ item }) => {
        const { id, subtotal, taxa_entrega, status, createdAt, total_items } = item;
        const total = (parseFloat(subtotal) || 0) + (parseFloat(taxa_entrega) || 0);

        const dateObj = new Date(createdAt);
        const date = dateObj.toLocaleDateString('pt-PT');
        
        return (
            //  ALTERADO: De Link para uma div com onClick para abrir o Modal
            <div 
                className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5 text-decoration-none text-dark"
                // Adiciona um estilo de cursor para indicar que  clicvel
                style={{ cursor: 'pointer' }} 
            >
                <div 
                    className="card shadow-sm h-100 rounded-3 border-0 animate__animated animate__fadeInUp"
                    onClick={() => handleOpenDetails(item)} //  CHAMA O HANDLER PARA ABRIR O MODAL
                >
                    
                    {/* Visual de Encomenda: ID e Status */}
                    <div className={`card-header p-3 bg-light d-flex justify-content-between align-items-center fw-bold ${getStatusColor(status)}`} style={{ borderBottom: `2px solid currentColor` }}>
                        <span className="mb-0">
                            <i className="fas fa-receipt me-2"></i>
                            #ENCOMENDA {id}
                        </span>
                        <span className="small">{status}</span>
                    </div>

                    <div className="card-body p-3 text-center">
                        <div className="d-flex justify-content-between align-items-center mb-2">
                            <p className="mb-0 small text-muted">
                                <i className="fas fa-calendar-alt me-1"></i> Data: {date}
                            </p>
                            <p className="mb-0 small text-muted">
                                <i className="fas fa-box me-1"></i> Itens: {total_items || '?'}
                            </p>
                        </div>
                        
                        <div className="mt-3 pt-3 border-top">
                            <h5 className="mb-0 fw-bold text-dark">
                                Total: {formatCurrency(total)}
                            </h5>
                        </div>
                    </div>
                </div>
            </div>
        );
    };

    // -------------------------------------------------------------------
    //  RENDERIZAO PRINCIPAL
    // -------------------------------------------------------------------
    return (
        <div
            className="d-flex flex-column"
            style={{
                position: "fixed",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: "#f4f4f4",
            }}
        >
            <Title title="Encomendas" />
            <Navbar/>

            <main
                className="flex-grow-1 w-100"
                style={{ overflowY: "auto", backgroundColor: "#fff" }}
            >
                
                <section className="p-4 pt-5 pb-5 container-fluid">
                    <h3 className="text-dark mb-5 fw-bolder text-center border-bottom pb-3"> Meu Histrico de Encomendas</h3>
                    <div className="row justify-content-start">
                        {
                            loadingEncomendas ? (
                                <div className="col-12"><Loader /></div>
                            ) : encomendas.length > 0 ? (
                                encomendas.map(item => (
                                    <EncomendaCard 
                                        key={item.id}
                                        item={item} 
                                    />
                                ))
                            ) : (
                                <div className="col-12 text-center text-muted py-5">
                                    <i className="fas fa-shopping-basket fa-3x mb-3"></i>
                                    <p>Voc no tem nenhuma encomenda no seu histrico.</p>
                                </div>
                            )
                        }
                    </div>
                </section>

                <Footer/>
            </main>
            
            {/*  NOVO: O Modal de Detalhes do Pedido */}
            <EncomendaDetailsModal
                show={showDetailsModal}
                handleClose={handleCloseDetails}
                encomenda={selectedEncomenda}
            />

        </div>
    );
};

export default Encomend;