import { useState, useEffect, useCallback } from "react";
import { useSelector } from "react-redux";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import { api, url } from "../axios/api";
import { numberFormat } from "../config/Config";
import Swal from "sweetalert2";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";

const BuyEncomend = () => {
    const navigate = useNavigate();
    const { promocaoId } = useParams();
    const [searchParams] = useSearchParams();
    const { user } = useSelector((state) => state.user);

    const [promocao, setPromocao] = useState(null);
    const [produto, setProduto] = useState(null);
    const [cliente, setCliente] = useState(null);

    const [loading, setLoading] = useState(true);
    const [sending, setSending] = useState(false);

    const initialQtd = parseInt(searchParams.get("qtd")) || 1;
    const [quantidade, setQuantidade] = useState(Math.max(1, initialQtd));

    const [showMap, setShowMap] = useState(false);
    const [coords, setCoords] = useState({ lat: null, lng: null });
    const [locationConfirmed, setLocationConfirmed] = useState(false);

    // -------------------------
    // CLCULOS
    // -------------------------
    const precoUnitarioFinal =
        produto && promocao
            ? produto.preco * (1 - promocao.desconto / 100)
            : 0;

    const subtotalItem = precoUnitarioFinal * quantidade;

    // -------------------------
    // LOAD DATA
    // -------------------------
    const loadData = useCallback(async () => {
        if (!user?.id || !promocaoId) return;

        try {
            const promoRes = await api.get(
                `/Promocao.php?action=read&id=${promocaoId}`
            );
            const promo = promoRes.data?.data;
            if (!promo) throw new Error("Promoo no encontrada");

            const prodRes = await api.get(
                `/Produto.php?action=read&id=${promo.produto_id}`
            );

            const cliRes = await api.get(
                `/Cliente.php?action=read&id=${user.id}`
            );

            setPromocao(promo);
            setProduto(prodRes.data.data);
            setCliente(cliRes.data.data);
        } catch (err) {
            Swal.fire(
                "Erro",
                err.message || "Erro ao carregar compra",
                "error"
            );
        } finally {
            setLoading(false);
        }
    }, [promocaoId, user?.id]);

    useEffect(() => {
        loadData();
        window.scrollTo(0, 0);
    }, [loadData]);

    // -------------------------
    // HANDLERS
    // -------------------------
    const handleQtdChange = (qtd) => {
        const v = parseInt(qtd);
        if (v < 1) return;
        setQuantidade(v);
    };

    const getUserLocation = () => {
        navigator.geolocation.getCurrentPosition(
            (pos) => {
                setCoords({
                    lat: pos.coords.latitude,
                    lng: pos.coords.longitude,
                });
                setShowMap(true);
            },
            () => {
                Swal.fire(
                    "Erro",
                    "No foi possvel obter sua localizao",
                    "error"
                );
            }
        );
    };

    const handleConfirmLocation = () => {
        setShowMap(false);
        setLocationConfirmed(true);
        Swal.fire("Ok", "Localizao confirmada", "success");
    };

    const handleSubmitOrder = async () => {
        if (!coords.lat || !coords.lng) {
            Swal.fire(
                "Aviso",
                "Selecione o endereo antes de finalizar",
                "warning"
            );
            return;
        }

        const payload = {
            action: "create",
            cliente_id: user.id,
            lat: coords.lat,
            lng: coords.lng,
            subtotal: subtotalItem,
            itens: [
                {
                    produto_id: produto.id,
                    qtd: quantidade,
                    preco: precoUnitarioFinal,
                    subtotal: subtotalItem,
                },
            ],
        };

        try {
            setSending(true);
            const res = await api.post("/Encomenda.php", payload);

            if (res.data.success) {
                Swal.fire("Sucesso", "Pedido realizado!", "success");
                navigate("/encomendas");
            } else {
                Swal.fire(
                    "Erro",
                    res.data.message || "Erro ao finalizar",
                    "error"
                );
            }
        } catch {
            Swal.fire(
                "Erro",
                "Falha ao comunicar com o servidor",
                "error"
            );
        } finally {
            setSending(false);
        }
    };

    // -------------------------
    // RENDER
    // -------------------------
    if (loading) {
        return (
            <div className="text-center py-5">
                <div className="spinner-border text-danger"></div>
            </div>
        );
    }

    return (
        <div className="d-flex flex-column min-vh-100">
            <Navbar />

            <main className="container py-5 flex-grow-1">
                <h3 className="mb-4">
                    Finalizar Compra
                    <span className="badge bg-danger ms-2">
                        {promocao.desconto}%
                    </span>
                </h3>

                <div className="row">
                    <div className="col-md-8">
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Produto</th>
                                    <th>Preo</th>
                                    <th>Qtd</th>
                                    <th>Subtotal</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>
                                        <img
                                            src={`${url}/uploads/${produto.foto}`}
                                            width={40}
                                            className="me-2 rounded"
                                        />
                                        {produto.nome}
                                    </td>
                                    <td>
                                        EUR{" "}
                                        {numberFormat(
                                            precoUnitarioFinal,
                                            2,
                                            ",",
                                            "."
                                        )}
                                    </td>
                                    <td>
                                        <input
                                            type="number"
                                            min="1"
                                            value={quantidade}
                                            onChange={(e) =>
                                                handleQtdChange(
                                                    e.target.value
                                                )
                                            }
                                            className="form-control form-control-sm"
                                            style={{ width: 70 }}
                                        />
                                    </td>
                                    <td>
                                        EUR{" "}
                                        {numberFormat(
                                            subtotalItem,
                                            2,
                                            ",",
                                            "."
                                        )}
                                    </td>
                                </tr>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <th colSpan="3" className="text-end">
                                        Total
                                    </th>
                                    <th className="text-danger">
                                        EUR{" "}
                                        {numberFormat(
                                            subtotalItem,
                                            2,
                                            ",",
                                            "."
                                        )}
                                    </th>
                                </tr>
                            </tfoot>
                        </table>

                        {!locationConfirmed ? (
                            <button
                                className="btn btn-primary"
                                onClick={getUserLocation}
                            >
                                Selecionar Endereo
                            </button>
                        ) : (
                            <button
                                className="btn btn-success"
                                onClick={handleSubmitOrder}
                                disabled={sending}
                            >
                                {sending
                                    ? "Enviando..."
                                    : "Finalizar Pedido"}
                            </button>
                        )}
                    </div>

                    <div className="col-md-4">
                        <div className="card p-3">
                            <p>
                                <b>Nome:</b> {cliente.nome}
                            </p>
                            <p>
                                <b>Telefone:</b> {cliente.telefone}
                            </p>
                            <p>
                                <b>Email:</b> {cliente.email}
                            </p>
                        </div>
                    </div>
                </div>

                {showMap && (
                    <div className="position-fixed top-0 start-0 w-100 h-100 bg-dark bg-opacity-75 d-flex align-items-center justify-content-center">
                        <div
                            className="bg-white p-3"
                            style={{ width: "90%", height: "90%" }}
                        >
                            <iframe
                                title="Mapa"
                                src={`https://maps.google.com/maps?q=${coords.lat},${coords.lng}&z=15&output=embed`}
                                width="100%"
                                height="100%"
                                style={{ border: 0 }}
                            ></iframe>

                            <div className="text-center mt-3">
                                <button
                                    className="btn btn-success me-2"
                                    onClick={handleConfirmLocation}
                                >
                                    Confirmar
                                </button>
                                <button
                                    className="btn btn-danger"
                                    onClick={() => setShowMap(false)}
                                >
                                    Cancelar
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </main>

            <Footer />
        </div>
    );
};

export default BuyEncomend;