import React, { useEffect, useState, useCallback } from "react";
import { useParams, useNavigate } from "react-router-dom"; // Adicionado useNavigate
import "animate.css";
import { hero1 } from "../static"; // Caminho da imagem esttica
import Navbar from "../components/Navbar"; // Caminho do Navbar
import Footer from "../components/Footer"; // Caminho do Footer
import { api, url } from "../axios/api"; // Configurao do Axios
import { numberFormat } from "../config/Config"; // Funo de formatao numrica

const DetalhesPromocao = () => {
  const { id } = useParams();
  const navigate = useNavigate(); // Hook para navegao programtica

  const [promocao, setPromocao] = useState(null);
  const [produto, setProduto] = useState(null);
  const [fotos, setFotos] = useState([]);
  const [fotoSelecionada, setFotoSelecionada] = useState(null);

  const [loadingPromocao, setLoadingPromocao] = useState(true);
  const [loadingFotos, setLoadingFotos] = useState(true);

  const [quantidade, setQuantidade] = useState(1);

  /**
   *  Buscar detalhes da promoo
   */
  const getPromocao = useCallback(async () => {
    if (!id) return;
    setLoadingPromocao(true);
    try {
      const res = await api.get(`/Promocao.php?action=read&id=${id}`);
      const data = res.data?.data;
      if (data) {
        setPromocao(data);
        getProduto(data.produto_id);
      } else {
        setPromocao(null);
        setProduto(null);
      }
    } catch (error) {
      console.error("Erro ao buscar promoo:", error);
      setPromocao(null);
    } finally {
      setLoadingPromocao(false);
    }
  }, [id]);

  /**
   *  Buscar produto vinculado  promoo
   */
  const getProduto = useCallback(async (produtoId) => {
    try {
      const res = await api.get(`/Produto.php?action=read&id=${produtoId}`);
      const produtoData = res.data?.data;
      setProduto(produtoData);
      if (produtoData) {
        getFotos(produtoData.id, produtoData.foto);
      }
    } catch (error) {
      console.error("Erro ao buscar produto:", error);
      setProduto(null);
    }
  }, []);

  /**
   *  Buscar fotos do produto
   */
  const getFotos = useCallback(async (produtoId, fotoPrincipal) => {
    setLoadingFotos(true);
    try {
      const res = await api.get(
        `/FotosProduto.php?action=listByProduto&produto_id=${produtoId}`
      );
      const fotosServidor = res.data?.data || [];

      // Garante que a foto principal  a primeira
      const listaFinal = [
        { foto: fotoPrincipal },
        ...fotosServidor.filter((f) => f.foto !== fotoPrincipal),
      ];

      setFotos(listaFinal);
      setFotoSelecionada(url + "/uploads/" + fotoPrincipal);
    } catch {
      // Fallback: usar apenas a foto principal
      setFotos([{ foto: fotoPrincipal }]);
      setFotoSelecionada(url + "/uploads/" + fotoPrincipal);
    } finally {
      setLoadingFotos(false);
    }
  }, []);

  useEffect(() => {
    getPromocao();
    window.scrollTo(0, 0);
  }, [getPromocao]);

  /**
   * Componente Loader simples
   */
  const Loader = ({ message = "A carregar..." }) => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status">
        <span className="visually-hidden">A carregar...</span>
      </div>
      <p className="mt-3 text-muted">{message}</p>
    </div>
  );

  /**
   *  LGICA ATUALIZADA: Redireciona para a tela de finalizao de compra imediata.
   */
  const handleComprarAgora = () => {
    if (produto && promocao) {
      // Navega para a nova rota, passando o ID da promoo e a quantidade como parmetro de busca (query string)
      navigate(`/encomenda/buy-encomend/${promocao.id}?qtd=${quantidade}`);
    } else {
      alert("Aguarde o carregamento completo da promoo antes de comprar.");
    }
  };

  /**
   * Calcula o preo final do produto com base no desconto da promoo
   */
  const precoFinal = produto
    ? (produto.preco * (1 - promocao.desconto / 100)).toFixed(2)
    : 0;

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />
      <main
        className="flex-grow-1 w-100"
        style={{ overflowY: "auto", backgroundColor: "#fff" }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
            {loadingPromocao
              ? " Detalhes da Promoo"
              : produto
              ? produto.nome
              : "Promoo No Encontrada"}
          </h3>

          {loadingPromocao && (
            <Loader message="A carregar detalhes da promoo..." />
          )}

          {produto && promocao && (
            <div className="row animate__animated animate__fadeIn">
              {/* FOTO */}
              <div className="col-12 col-md-6 mb-4 text-center">
                {fotoSelecionada && (
                  <img
                    src={fotoSelecionada}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "30em", objectFit: "cover" }}
                    alt={produto.nome}
                  />
                )}

                {/* MINIATURAS */}
                {!loadingFotos && fotos.length > 0 && (
                  <div className="d-flex justify-content-center mt-3 flex-wrap gap-2">
                    {fotos.map((foto, i) => (
                      <img
                        key={i}
                        src={url + "/uploads/" + foto.foto}
                        alt="foto produto"
                        className={`rounded shadow-sm ${
                          fotoSelecionada === url + "/uploads/" + foto.foto
                            ? "border border-danger border-3"
                            : ""
                        }`}
                        style={{
                          width: "5em",
                          height: "5em",
                          objectFit: "cover",
                          cursor: "pointer",
                        }}
                        onClick={() =>
                          setFotoSelecionada(url + "/uploads/" + foto.foto)
                        }
                      />
                    ))}
                  </div>
                )}
              </div>

              {/* DETALHES */}
              <div className="col-12 col-md-6 mb-4">
                <h2 className="fw-bolder text-primary mb-3">
                  {produto.nome}
                </h2>
                <p className="text-secondary lead">{produto.descricao}</p>

                <div className="my-4 border-top pt-3">
                  <span className="fs-5 text-decoration-line-through text-muted me-3">
                    EUR {numberFormat(produto.preco, 2, ",", ".")}
                  </span>
                  <span className="fs-4 fw-bold text-success">
                    EUR {numberFormat(precoFinal, 2, ",", ".")}
                  </span>
                </div>

                <div className="alert alert-warning py-2">
                   Desconto de <b>{promocao.desconto}%</b> vlido de{" "}
                  <b>{promocao.data_inicio}</b> at <b>{promocao.data_fim}</b>
                </div>

                <div className="d-flex align-items-center mb-4">
                  <button
                    className="btn btn-outline-secondary"
                    onClick={() => setQuantidade((q) => Math.max(1, q - 1))}
                  >
                    <i className="fas fa-minus"></i>
                  </button>
                  <span className="mx-3 fs-5 fw-bold">{quantidade}</span>
                  <button
                    className="btn btn-outline-secondary"
                    onClick={() => setQuantidade((q) => q + 1)}
                  >
                    <i className="fas fa-plus"></i>
                  </button>
                </div>

                <button
                  className="btn btn-danger btn-lg w-100 shadow-sm"
                  onClick={handleComprarAgora}
                >
                  <i className="fas fa-bolt me-2"></i> Comprar Agora
                </button>
              </div>
            </div>
          )}
        </section>

        {/* HERO PARALLAX */}
        <div
          className="d-flex align-items-center justify-content-center w-100"
          style={{
            height: "10em",
            backgroundImage: `url(${hero1})`,
            backgroundPosition: "center",
            backgroundSize: "cover",
            backgroundAttachment: "fixed",
          }}
        ></div>

        <Footer />
      </main>
    </div>
  );
};

export default DetalhesPromocao;