import { createHashRouter } from "react-router-dom";
import NotFound from "../views/404"; 
import Index from "../views/Index";
import Login from "../views/Login";
import Cadastro from "../views/Cadastro";
import Categorias from "../views/Category";
import Encomend from "../views/Encomend";
import DetalhesProduto from "../views/detalhesProduto";
import DetalhesPromocao from "../views/detalhesPromocao";
import ProdutosAll from "../views/listAllProd";
import PromocoesAll from "../views/listAllPromo";
import ProdutosCategory from "../views/listAllProdByCategory";
import ProdutosPesquisa from "../views/listAllSearcheds";
import NoticiasAll from "../views/NoticiasAll";
import DetalhesNoticia from "../views/detalhesNoticia";
import FAQ from "../views/FAQ";
import SobreNos from "../views/About";
import PoliticaPrivacidade from "../views/PoliticaPrivacidade";
import TermosCondicoes from "../views/TermosECondicoes";
import PrivateRoute from "../views/auth/PrivateRoute";
import Perfil from "../views/Perfil";
import Logout from "../views/auth/Logout";
import FinishEncomend from "../views/FinishEncomend";
import BuyEncomend from "../views/FinishEncomendPromo";

const routes = createHashRouter([
  {
    path: "/",
    element: <Index />,
  },
  {
    path: "/categorias",
    element: <Categorias />,
  },
  {
    path: "/encomendas",
     element: (
      <PrivateRoute>
        <Encomend />
      </PrivateRoute>
    ),
  },
  {
    path: "/encomendas/finalizar",
     element: (
      <PrivateRoute>
        <FinishEncomend />
      </PrivateRoute>
    ),
  },
  {
    path: "/encomenda/buy-encomend/:promocaoId/",
     element: (
      <PrivateRoute>
        <BuyEncomend />
      </PrivateRoute>
    ),
  },
  {
    path: "/produtos/",
    element: <ProdutosAll />,
  },
  {
    path: "/produtos/categoria/:categoria_id/",
    element: <ProdutosCategory />,
  },
  {
    path: "/produtos/search/",
    element: <ProdutosPesquisa />,
  },
  {
    path: "/produtos/detalhes/:id/",
    element: <DetalhesProduto />,
  },
  {
    path: "/promocoes/",
    element: <PromocoesAll />,
  },
  {
    path: "/blog-e-noticias/",
    element: <NoticiasAll />,
  },
  {
    path: "/noticias/detalhes/:id/",
    element: <DetalhesNoticia />,
  },
  {
    path: "/promocoes/detalhes/:id/",
    element: <DetalhesPromocao />,
  },
  {
    path: "/auth/login",
    element: <Login />,
  },
  {
    path: "/auth/logout",
    element: <Logout />,
  },
  {
    path: "/auth/cadastro",
    element: <Cadastro />,
  },
  {
    path: "/faq/",
    element: <FAQ />,
  },
  {
    path: "/about/",
    element: <SobreNos />,
  },
  {
    path: "/politica-privacidade/",
    element: <PoliticaPrivacidade />,
  },
  {
    path: "/termos-e-condicoes/",
    element: <TermosCondicoes />,
  },
  {
    path: "/perfil/",
    element: (
      <PrivateRoute>
        <Perfil />
      </PrivateRoute>
    ),
  },
  {
    path: "*",
    element: <NotFound />
  },
]);

export default routes;