import React, { useEffect, useState, useCallback } from "react";
import { useParams, Link } from "react-router-dom";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { hero1 } from "../static";
import moment from "moment";
import "moment/locale/pt";

const DetalhesNoticia = () => {
  const { id } = useParams();

  const [noticia, setNoticia] = useState(null);
  const [relacionadas, setRelacionadas] = useState([]);
  const [loadingNoticia, setLoadingNoticia] = useState(true);
  const [loadingRelacionadas, setLoadingRelacionadas] = useState(true);

  const getNoticiaDetails = useCallback(async () => {
    if (!id) return;
    setLoadingNoticia(true);
    try {
      const res = await api.get(`/Noticia.php?action=read&id=${id}`);
      const data = res.data.data;
      setNoticia(data);
      getNoticiasRelacionadas(data?.id);
    } catch (error) {
      console.error("Erro ao carregar notcia:", error);
      setNoticia(null);
    } finally {
      setLoadingNoticia(false);
    }
  }, [id]);

  const getNoticiasRelacionadas = useCallback(async (currentId) => {
    setLoadingRelacionadas(true);
    try {
      const res = await api.get(`/Noticia.php?action=listRecentsByLimit&limit=4`);
      const data = res.data.data || [];
      const filtered = data.filter((n) => n.id.toString() !== currentId.toString());
      setRelacionadas(filtered);
    } catch (error) {
      console.error("Erro ao buscar notcias relacionadas:", error);
      setRelacionadas([]);
    } finally {
      setLoadingRelacionadas(false);
    }
  }, []);

  useEffect(() => {
    getNoticiaDetails();
    window.scrollTo(0, 0);
  }, [getNoticiaDetails]);

  const Loader = ({ message = "A carregar..." }) => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status">
        <span className="visually-hidden">A carregar...</span>
      </div>
      <p className="mt-3 text-muted">{message}</p>
    </div>
  );

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />
      <main
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
        }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Detalhes da Notcia
          </h3>

          {loadingNoticia && <Loader message="A carregar detalhes da notcia..." />}

          {noticia && (
            <div className="row animate__animated animate__fadeIn">
              <div className="col-12 col-md-6 mb-4 text-center">
                <img
                  src={url + "/uploads/" + noticia.foto}
                  className="w-100 object-fit-cover rounded-3 shadow-sm"
                  style={{ height: "30em", objectFit: "cover" }}
                  alt={noticia.titulo}
                  onError={(e) => (e.target.src = url + "/uploads/no-image.jpg")}
                />
              </div>

              <div className="col-12 col-md-6 mb-4">
                <h2 className="fw-bolder text-primary mb-3">{noticia.titulo}</h2>
                <p className="text-muted small mb-4">
                  <i className="far fa-clock me-2"></i>
                  {moment(noticia.createdAt || noticia.created_at)
                    .locale("pt")
                    .format("DD MMMM YYYY, HH:mm")}
                </p>

                <p className="lead text-secondary">{noticia.descricao}</p>
              </div>
            </div>
          )}
        </section>

        {/*  HERO PARALLAX SECTION  */}
        <div
          className="d-flex align-items-center justify-content-center w-100"
          style={{
            height: "10em",
            backgroundImage: `url(${hero1})`,
            backgroundPosition: "center",
            backgroundSize: "cover",
            backgroundAttachment: "fixed",
            transition: "background-position 0.5s ease",
          }}
        ></div>

        {/*  NOTCIAS RELACIONADAS */}
        <section className="p-4 pt-5 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Mais Notcias
          </h3>
          <div className="row">
            {loadingRelacionadas ? (
              <div className="col-12">
                <Loader message="A carregar notcias relacionadas..." />
              </div>
            ) : relacionadas.length > 0 ? (
              relacionadas.map((item) => (
                <div
                  key={item.id}
                  className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5"
                >
                  <Link
                    to={`/noticias/detalhes/${item.id}/`}
                    className="text-decoration-none text-dark"
                    onClick={() => window.scrollTo(0, 0)}
                  >
                    <img
                      src={url + "/uploads/" + item.foto}
                      className="w-100 object-fit-cover rounded-3 shadow-sm"
                      style={{ height: "18em" }}
                      alt={item.titulo}
                      onError={(e) =>
                        (e.target.src = url + "/uploads/no-image.jpg")
                      }
                    />
                    <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                      {item.titulo}
                    </h5>
                    <small className="text-muted">
                      {moment(item.createdAt || item.created_at)
                        .locale("pt")
                        .format("DD MMM YYYY")}
                    </small>
                  </Link>

                  <div
                    className="position-absolute d-flex align-items-center"
                    style={{ top: "1em", left: "1em" }}
                  >
                    <span
                      style={{
                        background: "rgba(0,0,0,0.6)",
                        backdropFilter: "blur(3px)",
                      }}
                      className="fw-bold p-1 px-2 rounded-4 text-white small animate__animated animate__fadeInUp"
                    >
                      Relacionada
                    </span>
                  </div>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-newspaper fa-3x mb-3"></i>
                <p>Nenhuma notcia relacionada encontrada.</p>
              </div>
            )}
          </div>
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default DetalhesNoticia;