import React, { useState, useEffect, useCallback } from "react";
import { hero1, Logo } from "../static";
import { api } from "../axios/api";
import { Link, useNavigate } from "react-router-dom";

const Cadastro = () => {
    const [nome, setNome] = useState("");
    const [telefone, setTelefone] = useState("");
    const [email, setEmail] = useState("");
    const [endereco, setEndereco] = useState("");
    const [password, setPassword] = useState("");
    const [repeatPassword, setRepeatPassword] = useState("");

    const [paisId, setPaisId] = useState("");

    const [paises, setPaises] = useState([]);

    const [loadingPaises, setLoadingPaises] = useState(false);

    const [validationError, setValidationError] = useState("");
    const [successMessage, setSuccessMessage] = useState("");
    const [isLoading, setIsLoading] = useState(false);

    const navigate = useNavigate();

    // -------------------------
    // FETCH PASES
    // -------------------------
    const fetchPaises = useCallback(async () => {
        setLoadingPaises(true);
        try {
            const res = await api.get("Pais.php?action=listAll");
            setPaises(res.data?.data || []);
        } catch {
            setPaises([]);
        } finally {
            setLoadingPaises(false);
        }
    }, []);

    useEffect(() => {
        fetchPaises();
    }, [fetchPaises]);

    // -------------------------
    // CADASTRO
    // -------------------------
    const handleCadastro = async (e) => {
        e.preventDefault();
        setValidationError("");
        setSuccessMessage("");

        if (
            !nome ||
            !telefone ||
            !email ||
            !endereco ||
            !password ||
            !repeatPassword ||
            !paisId
        ) {
            setValidationError(
                "Todos os campos obrigatrios devem ser preenchidos."
            );
            return;
        }

        if (password !== repeatPassword) {
            setValidationError("As senhas no coincidem.");
            return;
        }

        setIsLoading(true);

        try {
            const res = await api.post("Cliente.php", {
                action: "create",
                nome,
                telefone,
                email,
                endereco,
                senha: password,
                pais_id: paisId
            });

            if (res.data.success) {
                setSuccessMessage(
                    "Cadastro realizado com sucesso! Redirecionando para o login..."
                );
                setTimeout(() => navigate("/auth/login"), 2000);
            } else {
                setValidationError(
                    res.data.message || "Erro ao cadastrar."
                );
            }
        } catch {
            setValidationError(
                "Erro de comunicao com o servidor."
            );
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div
            style={{
                position: "fixed",
                inset: 0,
                backgroundImage: `url(${hero1})`,
                backgroundSize: "cover",
            }}
        >
            <div
                className="d-flex align-items-center justify-content-center h-100"
                style={{ backgroundColor: "rgba(255,255,255,0.7)" }}
            >
                <div className="card shadow p-4" style={{ width: 550 }}>
                    <div className="text-center mb-3">
                        <img src={Logo} width={90} alt="Logo" />
                        <h4 className="fw-bold mt-2">Criar Conta</h4>
                    </div>

                    {validationError && (
                        <div className="alert alert-danger small">
                            {validationError}
                        </div>
                    )}

                    {successMessage && (
                        <div className="alert alert-success small">
                            {successMessage}
                        </div>
                    )}

                    <form onSubmit={handleCadastro}>
                        <input
                            className="form-control mb-2"
                            placeholder="Nome"
                            value={nome}
                            onChange={(e) =>
                                setNome(e.target.value)
                            }
                        />

                        <input
                            className="form-control mb-2"
                            placeholder="Telefone"
                            value={telefone}
                            onChange={(e) =>
                                setTelefone(e.target.value)
                            }
                        />

                        <input
                            className="form-control mb-2"
                            placeholder="Email"
                            value={email}
                            onChange={(e) =>
                                setEmail(e.target.value)
                            }
                        />

                        <input
                            className="form-control mb-2"
                            placeholder="Endereo completo"
                            value={endereco}
                            onChange={(e) =>
                                setEndereco(e.target.value)
                            }
                        />

                        <input
                            type="password"
                            className="form-control mb-2"
                            placeholder="Senha"
                            value={password}
                            onChange={(e) =>
                                setPassword(e.target.value)
                            }
                        />

                        <input
                            type="password"
                            className="form-control mb-3"
                            placeholder="Repetir senha"
                            value={repeatPassword}
                            onChange={(e) =>
                                setRepeatPassword(e.target.value)
                            }
                        />

                        <select
                            className="form-select mb-3"
                            value={paisId}
                            onChange={(e) =>
                                setPaisId(e.target.value)
                            }
                        >
                            <option value="">
                                Selecione o pas
                            </option>
                            {paises.map((p) => (
                                <option
                                    key={p.id}
                                    value={p.id}
                                >
                                    {p.nome}
                                </option>
                            ))}
                        </select>

                        <button
                            className="btn btn-dark w-100"
                            disabled={isLoading}
                        >
                            {isLoading
                                ? "Cadastrando..."
                                : "Registrar"}
                        </button>
                    </form>

                    <p className="text-center mt-3 small">
                        J tem conta?{" "}
                        <Link to="/auth/login">
                            Entrar
                        </Link>
                    </p>
                </div>
            </div>
        </div>
    );
};

export default Cadastro;
