import React, { useState } from "react";
// Assumindo que hero1 e Logo so definidos em '../static'
import { hero1, Logo } from "../static"; 
// Assumindo que 'api'  uma instncia configurada do axios
import {api} from '../axios/api'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom'; // Importao necessria para navegao
import { loginUser } from '../redux/userSlice'; // Redux Thunk action

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);
    const [validationError, setValidationError] = useState('');
    // Usaremos 'isLoading' e 'setIsLoading'
    const [isLoading, setIsLoading] = useState(false); 
    
    const dispatch = useDispatch();
    const navigate = useNavigate(); // Inicializa o hook de navegao

    const handleSubmit = async (e) => {
        e.preventDefault();
        setValidationError(''); // Limpa erros anteriores

        if (!email || !password) {
            setValidationError('Por favor, preencha todos os campos.');
            return;
        }

        setIsLoading(true); // Inicia o estado de loading
    
        try {
            // Chama a action do Redux, passando 'email' e 'password' (mapeado para 'senha' no thunk)
            const result = await dispatch(loginUser({ email, senha: password })).unwrap();
            
            if (result.success === false) {
                setValidationError(result.message || 'Credenciais invlidas. Verifique seu e-mail e senha.');
            } else {
                navigate('/');
            }
        } catch (err) {
            console.error("Erro no login:", err);
            setValidationError(err.message || 'Ocorreu um erro ao tentar fazer login. Tente novamente mais tarde.');
        } finally {
            setIsLoading(false); 
        }
    };

    const togglePasswordVisibility = () => {
        setShowPassword(!showPassword);
    };

    return (
        <>
            <div style={{
                position:"fixed",
                top:0,
                bottom:0,
                left:0,
                right:0,
                backgroundImage: `url(${hero1})`, 
                backgroundSize: 'cover',
                backgroundPosition: 'center',
            }}>
                 <div 
                    className="d-flex align-items-center justify-content-center h-100 py-3 overflow-auto" 
                >
                    <div 
                        className="card shadow-lg border-0 my-auto p-4" 
                        style={{ width:"430px" }}
                    >
                        <div className="card-header bg-white border-0 text-center">
                            <img 
                                src={Logo} 
                                alt="Logo da Elegance"
                                width={90}
                                className="object-fit-cover rounded-3 mb-2"
                            />
                            <h4 className="fw-bold mt-2">Bem-vindo de Volta</h4>
                            <p className="text-muted small">Faa login para acessar sua conta.</p>
                        </div>
                        
                        <div className="card-body pt-0">
                            
                            <form onSubmit={handleSubmit}>
                                
                                {/* EXIBIO DE ERRO DE VALIDAO */}
                                {validationError && (
                                    <div className={`alert ${validationError.includes('sucesso') ? 'alert-success' : 'alert-danger'} small`} role="alert">
                                        <i className={`fa-solid ${validationError.includes('sucesso') ? 'fa-check-circle' : 'fa-circle-exclamation'} me-2`}></i>
                                        {validationError}
                                    </div>
                                )}

                                {/* Campo E-mail */}
                                <div className="mb-3">
                                    <label htmlFor="email" className="form-label fw-medium">E-mail</label>
                                    <div className="input-group">
                                        <span className="input-group-text bg-transparent border-end-0">
                                            <i className="fa-solid fa-envelope text-muted"></i>
                                        </span>
                                        <input 
                                            type="email" 
                                            className="form-control border-start-0 ps-0" 
                                            id="email" 
                                            placeholder="seu.email@exemplo.com"
                                            value={email}
                                            onChange={(e) => setEmail(e.target.value)}
                                            required 
                                            disabled={isLoading}
                                        />
                                    </div>
                                </div>
                                
                                {/* Campo Senha */}
                                <div className="mb-3">
                                    <label htmlFor="password" className="form-label fw-medium">Senha</label>
                                    <div className="input-group">
                                        <span className="input-group-text bg-transparent border-end-0">
                                            <i className="fa-solid fa-lock text-muted"></i>
                                        </span>
                                        <input 
                                            type={showPassword ? "text" : "password"} 
                                            className="form-control border-start-0 ps-0" 
                                            id="password" 
                                            placeholder=""
                                            value={password}
                                            onChange={(e) => setPassword(e.target.value)}
                                            required 
                                            disabled={isLoading}
                                        />
                                        <button 
                                            className="btn btn-outline-secondary border-start-0" 
                                            type="button" 
                                            onClick={togglePasswordVisibility}
                                            aria-label={showPassword ? "Esconder senha" : "Mostrar senha"}
                                            disabled={isLoading}
                                        >
                                            <i className={`fa-solid ${showPassword ? 'fa-eye-slash' : 'fa-eye'}`}></i>
                                        </button>
                                    </div>
                                </div>

                                {/* Opes: Lembrar e Esqueceu a Senha */}
                                <div className="d-flex justify-content-between align-items-center mb-4 small">
                                    <div className="form-check">
                                        <input className="form-check-input" type="checkbox" id="rememberMe" disabled={isLoading} />
                                        <label className="form-check-label text-muted" htmlFor="rememberMe">
                                            Lembrar-me
                                        </label>
                                    </div>
                                    <a href="#" className="text-decoration-none text-danger fw-medium" style={{ pointerEvents: isLoading ? 'none' : 'auto' }}>
                                        Esqueceu a senha?
                                    </a>
                                </div>

                                {/*  BOTO DE LOGIN COM SPINNER */}
                                <button 
                                    type="submit" 
                                    className="btn btn-dark w-100 fw-bold mb-4"
                                    disabled={isLoading} 
                                >
                                    {isLoading ? (
                                        <>
                                            {/* Spinner do Bootstrap */}
                                            <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                                            ENTRANDO...
                                        </>
                                    ) : (
                                        <>
                                            <i className="fa-solid fa-right-to-bracket me-2"></i> FAZER LOGIN
                                        </>
                                    )}
                                </button>
                            </form>

                            {/* Divisor e Login Social */}
                            <div className="d-flex align-items-center mb-2">
                                <hr className="flex-grow-1" />
                                <span className="mx-3 text-muted small text-uppercase">ou</span>
                                <hr className="flex-grow-1" />
                            </div>
                            
                            <p className="text-center mt-2 small text-muted">
                                No tem uma conta? <Link to = "/auth/cadastro" className="text-dark fw-bold text-decoration-none" style={{ pointerEvents: isLoading ? 'none' : 'auto' }}>Crie uma aqui</Link>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Login;