import React, { useRef } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";

const PoliticaPrivacidade = () => {
  const scrollContainerRef = useRef(null);

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />

      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        {/*  Cabealho */}
        <section className="p-4 text-center bg-light border-bottom">
          <h3 className="fw-bold mb-3 text-dark"> Poltica de Privacidade</h3>
          <p className="text-muted mx-auto" style={{ maxWidth: "700px" }}>
            A <strong>Svent</strong> valoriza a sua privacidade e est
            comprometida em proteger os seus dados pessoais. Esta poltica
            explica como coletamos, usamos e protegemos as informaes que voc
            nos fornece.
          </p>
        </section>

        {/*  Contedo */}
        <section
          className="p-4 pb-5 animate__animated animate__fadeInUp"
          style={{ maxWidth: "900px", margin: "0 auto" }}
        >
          <h5 className="fw-bold mt-4">1. Coleta de Informaes</h5>
          <p className="text-muted">
            Coletamos informaes pessoais fornecidas diretamente por voc,
            como nome, email, telefone e endereo, quando realiza uma compra,
            cria uma conta ou entra em contato conosco.
          </p>

          <h5 className="fw-bold mt-4">2. Uso das Informaes</h5>
          <p className="text-muted">
            As informaes coletadas so utilizadas para processar pedidos,
            oferecer suporte ao cliente, melhorar nossos servios e enviar
            comunicaes relevantes sobre produtos e promoes da Svent.
          </p>

          <h5 className="fw-bold mt-4">3. Proteo de Dados</h5>
          <p className="text-muted">
            Adotamos medidas tcnicas e organizacionais adequadas para proteger
            suas informaes contra perda, uso indevido, acesso no autorizado
            e divulgao indevida.
          </p>

          <h5 className="fw-bold mt-4">4. Partilha de Informaes</h5>
          <p className="text-muted">
            A <strong>Svent</strong> no vende nem partilha os seus dados
            pessoais com terceiros, exceto quando necessrio para cumprir
            obrigaes legais ou processar pedidos (como empresas de entrega e
            servios de pagamento).
          </p>

          <h5 className="fw-bold mt-4">5. Cookies e Tecnologias Similares</h5>
          <p className="text-muted">
            Utilizamos cookies para melhorar a experincia de navegao e
            analisar o uso do nosso site. Pode desativar os cookies nas
            configuraes do seu navegador, mas isso poder limitar algumas
            funcionalidades.
          </p>

          <h5 className="fw-bold mt-4">6. Direitos do Utilizador</h5>
          <p className="text-muted">
            Voc tem o direito de aceder, corrigir, excluir ou limitar o uso dos
            seus dados pessoais. Para exercer esses direitos, entre em contato
            conosco atravs do email{" "}
            <a href="mailto:sventw2@gmail.com" className="text-danger">
              sventw2@gmail.com
            </a>
            .
          </p>

          <h5 className="fw-bold mt-4">7. Alteraes desta Poltica</h5>
          <p className="text-muted">
            A <strong>Svent</strong> reserva-se o direito de atualizar esta
            Poltica de Privacidade a qualquer momento. As alteraes sero
            publicadas nesta pgina com a data de atualizao revisada.
          </p>

          <h5 className="fw-bold mt-4">8. Contato</h5>
          <p className="text-muted mb-0">
            Caso tenha dvidas ou preocupaes sobre esta poltica, entre em
            contato conosco:
          </p>

          <div className="border rounded-3 p-3 mt-3 bg-light">
            <p className="mb-1">
              <strong>Empresa:</strong> Svent
            </p>
            <p className="mb-1">
              <strong>Endereo:</strong> Rua Doutor Sousa Campos, n 12, 3 Andar
            </p>
            <p className="mb-1">
              <strong>Cdigo Postal:</strong> 4490-627
            </p>
            <p className="mb-0">
              <strong>Email:</strong>{" "}
              <a href="mailto:sventw2@gmail.com" className="text-danger">
                sventw2@gmail.com
              </a>
            </p>
          </div>
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default PoliticaPrivacidade;