import React, { useEffect, useState, useRef, useCallback } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { useNavigate } from "react-router-dom";
import moment from "moment";
import "moment/locale/pt"; // localiza para portugus
import Title from "../components/Title";

const NoticiasAll = () => {
  const [noticias, setNoticias] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [limit] = useState(6);
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const scrollContainerRef = useRef(null);
  const navigate = useNavigate();

  // Buscar notcias com limite e offset
  const fetchNoticias = useCallback(
    async (isLoadMore = false) => {
      if (isLoadMore) setLoadingMore(true);
      else setLoading(true);

      try {
        const currentOffset = isLoadMore ? offset : 0;
        const res = await api.get(
          `/Noticia.php?action=listRecentsByLimit&limit=${limit}&offset=${currentOffset}`
        );

        const data = res?.data?.data || [];

        if (data.length === 0) {
          setHasMore(false);
          return;
        }

        if (isLoadMore) {
          setNoticias((prev) => [...prev, ...data]);
          setOffset((prev) => prev + limit);
        } else {
          setNoticias(data);
          setOffset(limit);
        }

        if (data.length < limit) setHasMore(false);
      } catch (error) {
        console.error("Erro ao buscar notcias:", error);
      } finally {
        if (isLoadMore) setLoadingMore(false);
        else setLoading(false);
      }
    },
    [limit, offset]
  );

  useEffect(() => {
    fetchNoticias(false);
  }, []);

  // Scroll infinito dentro do MAIN
  useEffect(() => {
    const container = scrollContainerRef.current;
    if (!container) return;

    const handleScroll = () => {
      const nearBottom =
        container.scrollTop + container.clientHeight >=
        container.scrollHeight - 100;

      if (nearBottom && hasMore && !loadingMore) {
        fetchNoticias(true);
      }
    };

    container.addEventListener("scroll", handleScroll);
    return () => container.removeEventListener("scroll", handleScroll);
  }, [fetchNoticias, hasMore, loadingMore]);

  const handleOpenDetails = (id) => navigate(`/noticias/detalhes/${id}/`);

  const Loader = ({ text = "A carregar notcias..." }) => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status"></div>
      <p className="mt-3 text-muted">{text}</p>
    </div>
  );

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Title title="Notcias" />
      <Navbar />

      {/* SCROLL DENTRO DO MAIN */}
      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Notcias Recentes
          </h3>

          <div className="row">
            {loading ? (
              <Loader />
            ) : noticias.length > 0 ? (
              noticias.map((item) => (
                <div
                  onClick={() => handleOpenDetails(item.id)}
                  key={item.id}
                  className="col-md-3 mb-5 position-relative"
                  role="button"
                >
                  <img
                    src={url + "/uploads/" + item.foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "16em" }}
                    alt={item.titulo}
                    onError={(e) =>
                      (e.target.src = url + "/uploads/no-image.jpg")
                    }
                  />
                  <h5 className="mt-3 fw-semibold text-truncate">
                    {item.titulo}
                  </h5>
                  <small className="text-muted d-block mb-1">
                    <i className="far fa-clock me-1"></i>
                    {moment(item.createdAt || item.created_at)
                      .locale("pt")
                      .format("DD MMMM YYYY, HH:mm")}
                  </small>
                  <p
                    className="text-muted small"
                    style={{
                      height: "3em",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                    }}
                  >
                    {item.descricao}
                  </p>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-newspaper fa-3x mb-3"></i>
                <p>Nenhuma notcia encontrada.</p>
              </div>
            )}
          </div>

          {loadingMore && <Loader text="Carregando mais notcias..." />}

          {!hasMore && noticias.length > 0 && (
            <p className="text-center text-muted mt-4">
               Todas as notcias foram carregadas.
            </p>
          )}
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default NoticiasAll;