import React, { useEffect, useState, useRef, useCallback } from "react";
import "animate.css";
import { hero1 } from "../static";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { numberFormat } from "../config/Config";
import { useNavigate } from "react-router-dom";

const PromocoesAll = () => {
  const [promocoes, setPromocoes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);

  const navigate = useNavigate();
  const mainRef = useRef(null);
  const limit = 8;

  const getPromocoes = async (newOffset = 0) => {
    if (!hasMore && newOffset !== 0) return;

    setLoading(true);
    try {
      const res = await api.get(
        `/Promocao.php?action=listAllActivesByLimit&limit=${limit}&offset=${newOffset}`
      );
      const dados = res.data.data || [];

      if (newOffset === 0) {
        setPromocoes(dados);
      } else {
        setPromocoes((prev) => [...prev, ...dados]);
      }

      // Se no retornou mais dados, encerra o carregamento
      if (dados.length < limit) {
        setHasMore(false);
      }
    } catch (error) {
      console.error("Erro ao buscar promoes:", error);
      setHasMore(false);
    } finally {
      setLoading(false);
    }
  };

  // Detecta rolagem no <main>
  const handleScroll = useCallback(() => {
    const main = mainRef.current;
    if (!main || loading || !hasMore) return;

    const { scrollTop, scrollHeight, clientHeight } = main;
    if (scrollTop + clientHeight >= scrollHeight - 150) {
      const newOffset = offset + limit;
      setOffset(newOffset);
      getPromocoes(newOffset);
    }
  }, [offset, loading, hasMore]);

  useEffect(() => {
    getPromocoes(0);
  }, []);

  useEffect(() => {
    const main = mainRef.current;
    if (main) main.addEventListener("scroll", handleScroll);
    return () => {
      if (main) main.removeEventListener("scroll", handleScroll);
    };
  }, [handleScroll]);

  const handleOpenPromoDetails = (id) => {
    navigate(`/promocoes/detalhes/${id}/`);
  };

  const calculatePrice = (originalPrice, discount) => {
    const finalPrice = originalPrice * (1 - discount / 100);
    return numberFormat(finalPrice, 2, ",", ".");
  };

  const Loader = () => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status">
        <span className="visually-hidden">A carregar...</span>
      </div>
      <p className="mt-3 text-muted">A carregar promoes...</p>
    </div>
  );

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />

      {/* REA SCROLLVEL */}
      <main
        ref={mainRef}
        className="flex-grow-1 w-100"
        style={{ overflowY: "auto", backgroundColor: "#fff" }}
      >
        {/*  PROMOES */}
        <section className="p-4 pt-5 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Promoes e Descontos
          </h3>

          <div className="row">
            {promocoes.length > 0 ? (
              promocoes.map((promo) => (
                <div
                  key={promo.id}
                  onClick={() => handleOpenPromoDetails(promo.id)}
                  role="button"
                  className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5"
                >
                  <img
                    src={url + "/uploads/" + promo.produto_foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "18em" }}
                    alt={promo.produto_nome}
                  />

                  <div className="position-absolute top-0 end-0 bg-danger text-white rounded-bottom-left-3 py-1 px-3 fw-bold small shadow-sm">
                    -{numberFormat(promo.desconto, 0, ",", ".")} %
                  </div>

                  <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                    {promo.produto_nome}
                  </h5>

                  <div className="d-flex align-items-center">
                    <span className="text-muted text-decoration-line-through small me-2">
                      EUR {numberFormat(promo.produto_preco || 50, 2, ",", ".")}
                    </span>

                    <b className="text-dark fs-5">
                      EUR {calculatePrice(promo.produto_preco, promo.desconto)}
                    </b>
                  </div>
                </div>
              ))
            ) : loading ? (
              <div className="col-12">
                <Loader />
              </div>
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-tag fa-3x mb-3"></i>
                <p>Nenhuma promoo ativa no momento. Fique de olho!</p>
              </div>
            )}
          </div>

          {/* Indicador de carregamento infinito */}
          {loading && hasMore && (
            <div className="text-center mt-3">
              <Loader />
            </div>
          )}
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default PromocoesAll;
