import React, { useEffect, useState, useRef } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api } from "../axios/api";

const FAQ = () => {
  const [faqList, setFaqList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeIndex, setActiveIndex] = useState(null);
  const scrollContainerRef = useRef(null);

  //  Buscar FAQ da API
  const fetchFAQ = async () => {
    try {
      const res = await api.get("/Faq.php?action=listAll");
      setFaqList(res.data?.data || []);
    } catch (error) {
      console.error("Erro ao carregar FAQ:", error);
      setFaqList([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchFAQ();
  }, []);

  const Loader = ({ text = "A carregar perguntas frequentes..." }) => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status"></div>
      <p className="mt-3 text-muted">{text}</p>
    </div>
  );

  const toggleFAQ = (index) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />

      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Perguntas Frequentes (FAQ)
          </h3>

          {loading ? (
            <Loader />
          ) : faqList.length > 0 ? (
            <div className="accordion animate__animated animate__fadeIn">
              {faqList.map((faq, index) => (
                <div key={index} className="mb-3 border rounded-3 shadow-sm">
                  <button
                    className="w-100 text-start p-3 bg-light border-0 fw-semibold d-flex justify-content-between align-items-center"
                    onClick={() => toggleFAQ(index)}
                    style={{ fontSize: "1rem" }}
                  >
                    <span>{faq.pergunta}</span>
                    <i
                      className={`fas ${
                        activeIndex === index
                          ? "fa-chevron-up text-danger"
                          : "fa-chevron-down text-secondary"
                      }`}
                    ></i>
                  </button>

                  <div
                    className={`px-3 overflow-hidden transition-all ${
                      activeIndex === index
                        ? "animate__fadeInDown"
                        : "animate__fadeOutUp d-none"
                    }`}
                  >
                    <p className="py-3 text-muted">{faq.resposta}</p>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center text-muted py-5">
              <i className="fas fa-question-circle fa-3x mb-3"></i>
              <p>Nenhuma pergunta frequente disponvel no momento.</p>
            </div>
          )}
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default FAQ;
