import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { logout, checkAuth } from "../../redux/userSlice";
import { SignOut } from "../../static";

const Logout = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);

  useEffect(() => {
    const handleLogout = async () => {
      //  Se no houver user no Redux, tenta restaurar do localStorage
      if (!user) {
        await dispatch(checkAuth());
      }

      //  Chama o logout no backend e limpa Redux/Storage
      if (user?.id) {
        dispatch(logout(user.id));
        setTimeout(() => navigate("/auth/login"), 2000);
      } else {
        localStorage.removeItem("user");
      }

      //  Redireciona aps 2s
    };

    handleLogout();
  }, [dispatch, navigate, user]);

  return (
    <div>
      <div className="position-fixed top-0 bottom-0 end-0 start-0 z-0 bg-pattern" />
      <div className="auth-pattern-shapes d-none d-lg-block" />
      <div className="auth-pattern-outline d-none d-lg-block" />
      <div className="auth-pattern-shape extra d-none d-lg-block" />
      <div className="auth-pattern-extra d-none d-lg-block" />
      <div className="container">
        <div className="row justify-content-center align-items-center min-vh-100 pt-20 pb-10">
          <div className="col-12 col-md-8 col-lg-6 col-xl-5">
            <div className="card mx-xxl-8 shadow-none">
              <div className="card-body p-8 text-center">
                <div className="mb-6">
                  <h3 className="fw-medium">Voc saiu da conta</h3>
                  <p className="text-muted">
                    Obrigado por visitar! Voc ser redirecionado para a pgina de login.
                  </p>
                </div>
                <div className="mb-4">
                  <img
                    src={SignOut}
                    alt="Ilustrao de sada"
                    className="img-fluid w-256px"
                  />
                </div>
              </div>
            </div>
            <p className="position-relative text-center text-uppercase fs-13 mb-0">
               7vntz. TODOS OS DIREITOS RESERVADOS
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Logout;
