import React, { useRef, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import Title from "../components/Title";

const Perfil = () => {
  const scrollContainerRef = useRef(null);
  const { user } = useSelector((state) => state.user);
  const [dados, setDados] = useState(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [senhaData, setSenhaData] = useState({ atual: "", nova: "", confirmar: "" });
  const [fotoPreview, setFotoPreview] = useState(null);

  useEffect(() => {
    if (user?.id) fetchPerfil(user.id);
  }, [user?.id]);

  const fetchPerfil = async (id) => {
    try {
      const response = await api.get(`/Cliente.php?action=read&id=${id}`);
      if (response.data.success) setDados(response.data.data);
    } catch (error) {
      console.error("Erro ao carregar perfil:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      const response = await api.post("/Cliente.php", {
        id: dados.id,
        nome: dados.nome,
        telefone: dados.telefone,
        email: dados.email,
        action: "update",
      });
      if (response.data.success) alert(" Perfil atualizado com sucesso!");
    } catch (err) {
      console.error(err);
      alert(" Erro ao atualizar perfil!");
    } finally {
      setSaving(false);
    }
  };

  const handleFotoChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    setFotoPreview(URL.createObjectURL(file));

    const formData = new FormData();
    formData.append("action", "updateFoto");
    formData.append("foto", file);
    formData.append("id", dados.id);

    try {
      const response = await api.post("/Cliente.php", formData);
      if (response.data.success) {
        alert(" Foto atualizada!");
        fetchPerfil(dados.id);
      }
    } catch (err) {
      console.error(err);
      alert(" Erro ao atualizar foto!");
    }
  };

  // Alterar senha
const handleSenhaChange = async () => {
  const { atual, nova, confirmar } = senhaData;
  if (!atual || !nova || !confirmar) return alert("Preencha todos os campos!");
  if (nova !== confirmar) return alert("As novas senhas no coincidem!");

  try {
    const response = await api.post("/Cliente.php", {
      id: dados.id,
      action: "updatePassword",
      nova_senha: nova,
    });

    if (response.data.success) {
      alert(" Senha alterada com sucesso!");
      setSenhaData({ atual: "", nova: "", confirmar: "" });
    } else {
      alert(" Erro ao atualizar senha!");
    }
  } catch (err) {
    console.error(err);
    alert(" Erro ao alterar senha!");
  }
};


  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Title title="Meu Perfil" />
      <Navbar />

      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        <section className="p-4 text-center bg-light border-bottom">
          <h3 className="fw-bold mb-3 text-dark"> Meu Perfil</h3>
          <p className="text-muted mx-auto" style={{ maxWidth: "700px" }}>
            Atualize suas informaes, foto e senha da conta Svent.
          </p>
        </section>

        <section
          className="p-4 pb-5 animate__animated animate__fadeInUp"
          style={{ maxWidth: "900px", margin: "0 auto" }}
        >
          {loading ? (
            <div className="text-center py-5">
              <div className="spinner-border text-dark" role="status"></div>
              <p className="mt-3 text-muted">Carregando informaes...</p>
            </div>
          ) : dados ? (
            <>
              {/* Foto */}
              <div className="text-center mb-4">
                <img
                  src={
                    fotoPreview
                      ? fotoPreview
                      : dados.foto
                      ? `${url}/uploads/${dados.foto}`
                      : `${url}/static/default-avatar.png`
                  }
                  alt="Perfil"
                  className="rounded-circle border"
                  width="120"
                  height="120"
                />
                <div className="mt-2">
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleFotoChange}
                    className="form-control form-control-sm w-auto d-inline-block"
                  />
                </div>
              </div>

              {/* Formulrio principal */}
              <div className="card border-0 shadow-sm p-4 mb-4">
                <h5 className="fw-bold mb-3"> Informaes Pessoais</h5>

                <div className="row g-3">
                  <div className="col-md-6">
                    <label className="form-label">Nome</label>
                    <input
                      type="text"
                      className="form-control"
                      value={dados.nome || ""}
                      onChange={(e) => setDados({ ...dados, nome: e.target.value })}
                    />
                  </div>

                  <div className="col-md-6">
                    <label className="form-label">Telefone</label>
                    <input
                      type="text"
                      className="form-control"
                      value={dados.telefone || ""}
                      onChange={(e) => setDados({ ...dados, telefone: e.target.value })}
                    />
                  </div>

                  <div className="col-12">
                    <label className="form-label">Email</label>
                    <input
                      type="email"
                      className="form-control"
                      value={dados.email || ""}
                      onChange={(e) => setDados({ ...dados, email: e.target.value })}
                    />
                  </div>

                  <div className="col-12 text-end">
                    <button
                      onClick={handleSave}
                      className="btn btn-dark px-4"
                      disabled={saving}
                    >
                      {saving ? "Salvando..." : "Salvar Alteraes"}
                    </button>
                  </div>
                </div>
              </div>

              {/* Alterar senha */}
              <div className="card border-0 shadow-sm p-4">
                <h5 className="fw-bold mb-3"> Alterar Senha</h5>

                <div className="row g-3">
                  <div className="col-md-4">
                    <label className="form-label">Senha Atual</label>
                    <input
                      type="password"
                      className="form-control"
                      value={senhaData.atual}
                      onChange={(e) =>
                        setSenhaData({ ...senhaData, atual: e.target.value })
                      }
                    />
                  </div>
                  <div className="col-md-4">
                    <label className="form-label">Nova Senha</label>
                    <input
                      type="password"
                      className="form-control"
                      value={senhaData.nova}
                      onChange={(e) =>
                        setSenhaData({ ...senhaData, nova: e.target.value })
                      }
                    />
                  </div>
                  <div className="col-md-4">
                    <label className="form-label">Confirmar Nova Senha</label>
                    <input
                      type="password"
                      className="form-control"
                      value={senhaData.confirmar}
                      onChange={(e) =>
                        setSenhaData({ ...senhaData, confirmar: e.target.value })
                      }
                    />
                  </div>

                  <div className="col-12 text-end">
                    <button onClick={handleSenhaChange} className="btn btn-danger px-4">
                      Atualizar Senha
                    </button>
                  </div>
                </div>
              </div>
            </>
          ) : (
            <p className="text-center text-muted py-5">
              Nenhum dado encontrado para este usurio.
            </p>
          )}
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default Perfil;