import { useState, useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { api, url } from '../axios/api';
import { numberFormat } from '../config/Config';
import Swal from 'sweetalert2';

const FinishEncomend = () => {
    const [cartItems, setCartItems] = useState([]);
    const [loadingCart, setLoadingCart] = useState(false);
    const [sending, setSending] = useState(false);
    const [totalCartPrice, setTotalCartPrice] = useState(0);
    const [cliente, setClienteData] = useState(null);
    const [showMap, setShowMap] = useState(false);
    const [coords, setCoords] = useState({ lat: null, lng: null });
    const [locationConfirmed, setLocationConfirmed] = useState(false);

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { user } = useSelector((state) => state.user);

    const calculateTotalPrice = (items) =>
        items.reduce((acc, item) => acc + parseFloat(item.preco) * parseInt(item.qtd), 0);

    const getCartItems = useCallback(async (clienteId) => {
        if (!clienteId) return;
        setLoadingCart(true);
        try {
            const res = await api.get(`/Carrinho.php?action=listByCliente&cliente_id=${clienteId}`);
            const items = res.data?.data || [];
            setCartItems(items);
            setTotalCartPrice(
                res.data?.total_carrinho
                    ? parseFloat(res.data.total_carrinho)
                    : calculateTotalPrice(items)
            );
        } catch {
            Swal.fire('Erro', 'No foi possvel carregar o carrinho.', 'error');
        } finally {
            setLoadingCart(false);
        }
    }, []);

    const getCliente = useCallback(async (clienteId) => {
        try {
            const res = await api.get(`/Cliente.php?action=read&id=${clienteId}`);
            setClienteData(res.data.data);
        } catch {}
    }, []);

    const handleQtdChange = (id, qtd) => {
        if (qtd < 1) return;
        const updated = cartItems.map(i =>
            i.id === id ? { ...i, qtd } : i
        );
        setCartItems(updated);
        setTotalCartPrice(calculateTotalPrice(updated));
    };

    const getUserLocation = () => {
        navigator.geolocation.getCurrentPosition(
            pos => {
                setCoords({ lat: pos.coords.latitude, lng: pos.coords.longitude });
                setShowMap(true);
            },
            () => Swal.fire('Erro', 'No foi possvel obter a localizao.', 'error')
        );
    };

    const handleConfirmLocation = () => {
        setShowMap(false);
        setLocationConfirmed(true);
    };

    const handleSubmitOrder = async () => {
        if (!cartItems.length) {
            Swal.fire('Aviso', 'O carrinho est vazio.', 'warning');
            return;
        }
        if (!coords.lat || !coords.lng) {
            Swal.fire('Aviso', 'Selecione o endereo.', 'warning');
            return;
        }

        const itens = cartItems.map(item => ({
            produto_id: item.produto_id,
            qtd: item.qtd,
            preco: parseFloat(item.preco),
            subtotal: item.preco * item.qtd
        }));

        const payload = {
            action: 'create',
            cliente_id: user.id,
            lat: coords.lat,
            lng: coords.lng,
            subtotal: totalCartPrice,
            itens
        };

        try {
            setSending(true);
            const res = await api.post('/Encomenda.php', payload);
            if (res.data.success) {
                await api.post('/Carrinho.php', { action: 'clearCart', cliente_id: user.id });
                Swal.fire('Sucesso', 'Pedido finalizado com sucesso.', 'success');
                navigate('/encomendas');
            } else {
                Swal.fire('Erro', res.data.message, 'error');
            }
        } catch {
            Swal.fire('Erro', 'Erro ao finalizar pedido.', 'error');
        } finally {
            setSending(false);
        }
    };

    const handleRemoveItem = async (id) => {
        const confirm = await Swal.fire({
            title: 'Remover item?',
            icon: 'warning',
            showCancelButton: true
        });
        if (!confirm.isConfirmed) return;

        const res = await api.post('/Carrinho.php', {
            action: 'delete',
            id,
            cliente_id: user.id
        });

        if (res.data.success) {
            const updated = cartItems.filter(i => i.id !== id);
            setCartItems(updated);
            setTotalCartPrice(calculateTotalPrice(updated));
        }
    };

    useEffect(() => {
        if (user?.id) {
            getCartItems(user.id);
            getCliente(user.id);
        }
    }, [user?.id, getCartItems, getCliente]);

    const isCartEmpty = cartItems.length === 0;

    return (
        <div className="p-5 pt-3">
            <div className="row">
                <div className="col-md-8">
                    <h4>Detalhes do Carrinho</h4>

                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Preo</th>
                                <th>Qtd</th>
                                <th>Subtotal</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {isCartEmpty ? (
                                <tr><td colSpan="5">Carrinho vazio</td></tr>
                            ) : cartItems.map(item => (
                                <tr key={item.id}>
                                    <td>
                                        <img src={`${url}/uploads/${item.foto}`} width="40" alt="" /> {item.produto_nome}
                                    </td>
                                    <td>EUR {numberFormat(item.preco,2,',','.')}</td>
                                    <td>
                                        <input
                                            type="number"
                                            value={item.qtd}
                                            min="1"
                                            onChange={e => handleQtdChange(item.id, +e.target.value)}
                                            style={{ width: 60 }}
                                        />
                                    </td>
                                    <td>EUR {numberFormat(item.preco * item.qtd,2,',','.')}</td>
                                    <td>
                                        <button className="btn btn-danger btn-sm"
                                            onClick={() => handleRemoveItem(item.id)}>
                                            <i className="fas fa-trash"></i>
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                        <tfoot>
                            <tr>
                                <th colSpan="3" className="text-end">Total</th>
                                <th colSpan="2">
                                    EUR {numberFormat(totalCartPrice,2,',','.')}
                                </th>
                            </tr>
                        </tfoot>
                    </table>

                    {!locationConfirmed ? (
                        <button className="btn btn-primary" onClick={getUserLocation} disabled={isCartEmpty}>
                            Selecionar Endereo
                        </button>
                    ) : (
                        <button className="btn btn-success" onClick={handleSubmitOrder}>
                            Finalizar Pedido
                        </button>
                    )}
                </div>

                <div className="col-md-4">
                    <h4>Entrega</h4>
                    <div className="card p-3">
                        {cliente && (
                            <>
                                <p><b>Nome:</b> {cliente.nome}</p>
                                <p><b>Telefone:</b> {cliente.telefone}</p>
                                <p><b>Email:</b> {cliente.email}</p>
                            </>
                        )}
                    </div>
                </div>
            </div>

            {showMap && (
                <div className="position-fixed top-0 start-0 w-100 h-100 bg-dark bg-opacity-75 d-flex flex-column align-items-center justify-content-center">
                    <iframe
                        src={`https://maps.google.com/maps?q=${coords.lat},${coords.lng}&z=15&output=embed`}
                        width="90%"
                        height="80%"
                        style={{ border: 0 }}
                    />
                    <div className="mt-3">
                        <button className="btn btn-success me-2" onClick={handleConfirmLocation}>
                            Confirmar
                        </button>
                        <button className="btn btn-danger" onClick={() => setShowMap(false)}>
                            Cancelar
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default FinishEncomend;