import { useState, useEffect, useCallback } from "react";
import { Logo } from "../static";
import { api, url } from "../axios/api"; // Importei 'url' para as imagens do carrinho
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../redux/userSlice';
import Swal from 'sweetalert2'
import { numberFormat } from '../config/Config'; // Importao adicionada para formatao de preo

const NAV_LINKS = [
    { name: "PGINA INICIAL", link: "/" },
    { name: "EXPLORAR", link: "/produtos" },
    { name: "CATEGORIAS", link: "/categorias" },
    { name: "ENCOMENDAS", link: "/encomendas" },
    { name: "NOTCIAS", link: "/blog-e-noticias" },
];

const Navbar = () => {
    const [isSearchOpen, setIsSearchOpen] = useState(false);
    const [keyword, setKeyword] = useState("");
    const [CATEGORIAS_PRINCIPAIS, setCategorias] = useState([]);
    
    //  Novos estados para o carrinho
    const [cartItems, setCartItems] = useState([]);
    const [loadingCart, setLoadingCart] = useState(false);
    const [totalCartPrice, setTotalCartPrice] = useState(0);

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { user } = useSelector((state) => state.user)
    
    // O contador de itens agora  derivado do estado do carrinho
    const cartItemCount = cartItems.length;

    //  Buscar itens do carrinho
    const getCartItems = useCallback(async (clienteId) => {
        if (!clienteId) {
            setCartItems([]);
            setTotalCartPrice(0);
            return;
        }

        setLoadingCart(true);
        try {
            // Requisio para listar itens do carrinho pelo ID do cliente
            const res = await api.get(`/Carrinho.php?action=listByCliente&cliente_id=${clienteId}`);
            const items = res.data?.data || [];
            
            setCartItems(items);
            
            // Calcular o preo total
            const total = items.reduce((acc, item) => acc + (parseFloat(item.preco) * parseInt(item.qtd)), 0);
            setTotalCartPrice(total);

        } catch (err) {
            console.error("Erro ao buscar itens do carrinho:", err);
            setCartItems([]);
            setTotalCartPrice(0);
        } finally {
            setLoadingCart(false);
        }
    }, []);

    //  Efeito para buscar itens do carrinho quando o usurio loga ou desloga
    useEffect(() => {
        if (user?.id) {
            getCartItems(user.id);
        } else {
            getCartItems(null); // Limpar o carrinho se deslogado
        }
    }, [user?.id, getCartItems]); // Dependncia do ID do usurio e da funo memoizada

    //  Funes originais
    const getCategorias = async () => {
        try {
            const res = await api.get(`/Categoria.php?action=listSix`);
            setCategorias(res.data.data);
        } catch (err) {
            console.log(err);
        }
    };

    const handleOpenProds = (id) => {
        navigate(`/produtos/categoria/${id}/`);
    };

    const handleSearchSubmit = (e) => {
        e.preventDefault();
        if (keyword.trim() !== "") {
            navigate(`/produtos/search/?q=${encodeURIComponent(keyword.trim())}`);
            setIsSearchOpen(false);
            setKeyword("");
        }
    };

    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.key === "Escape") setIsSearchOpen(false);
        };
        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, []);

    useEffect(() => {
        getCategorias();
    }, []);

    const handleLogout = () => {
        Swal.fire({
            title: "Deseja terminar a sesso?",
            text: "Ter de iniciar novamente ao voltar ao site.",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sim, terminar sesso",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                dispatch(logout(user.id)); // ou sua funo de logout
                Swal.fire({
                    title: "Sesso terminada!",
                    text: "Saiu com sucesso da sua conta.",
                    icon: "success",
                    timer: 2000,
                    showConfirmButton: false,
                });
            }
        });
    };

    //  Componente Auxiliar de Carregamento para o Offcanvas
    const CartLoader = () => (
        <div className="text-center py-5">
            <div className="spinner-border text-danger" role="status">
                <span className="visually-hidden">A carregar...</span>
            </div>
            <p className="mt-3 text-muted small">A carregar itens do carrinho...</p>
        </div>
    );


    return (
        <>
            {/* HEADER */}
            <header className="flex-shrink-0 shadow-sm bg-white" style={{ zIndex: 100 }}>
                {/*  Topbar */}
                <div
                    className="text-light py-2 d-flex justify-content-center align-items-center"
                    style={{
                        backgroundColor: "#202020ff",
                        fontSize: "0.85rem",
                        overflow: "hidden",
                        whiteSpace: "nowrap",
                    }}
                >
                    <marquee behavior="scroll" direction="left" scrollamount="5">
                         Bem-vindo  nossa plataforma oficial  mantenha-se atualizado com as
                        ltimas notcias, produtos e oportunidades exclusivas. 
                    </marquee>
                </div>

                {/*  Navbar principal */}
                <nav className="navbar navbar-expand-lg py-3 px-4">
                    <div className="container-fluid p-0">
                        {/* Boto Hamburguer */}
                        <button
                            className="navbar-toggler p-0 border-0 d-lg-none"
                            type="button"
                            data-bs-toggle="offcanvas"
                            data-bs-target="#mobileMenu"
                            aria-controls="mobileMenu"
                        >
                            <i className="fa-solid fa-bars fs-5"></i>
                        </button>

                        {/* Links Desktop */}
                        <div className="d-none d-lg-flex order-lg-1">
                            <ul
                                className="navbar-nav gap-4 fw-medium text-uppercase"
                                style={{ fontSize: "0.9rem" }}
                            >
                                {NAV_LINKS.map((item, index) => (
                                    <li className="nav-item" key={index}>
                                        <Link className="nav-link text-dark p-0" to={item.link}>
                                            {item.name}
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        </div>

                        {/* Logo */}
                        <Link className="navbar-brand mx-auto me-lg-4 p-0 order-lg-2" to="/">
                            <img
                                src={Logo}
                                alt="Logo"
                                height="40"
                                style={{ objectFit: "cover" }}
                                className="rounded-3 object-fit-cover"
                            />
                        </Link>

                        {/* cones  direita */}
                        <div className="d-flex align-items-center gap-3 order-lg-3">
                            <button
                                className="btn btn-link text-dark p-0 border-0"
                                onClick={() => setIsSearchOpen(!isSearchOpen)}
                            >
                                <i className="fa-solid fa-magnifying-glass fs-5"></i>
                            </button>
                            <Link to="/perfil" className="text-dark nav-link p-0 d-sm-block">
                                <i className="fa-regular fa-user fs-5"></i>
                            </Link>
                            {user && (
                                <button
                                    onClick={handleLogout}
                                    className="text-dark nav-link p-0 d-sm-block"
                                    title="Terminar Sesso"
                                >
                                    <i className="fa-solid fa-power-off fs-5 text-danger"></i>
                                </button>
                            )}
                            <button
                                className="btn btn-dark text-white rounded-pill d-flex align-items-center py-2 px-3 position-relative"
                                data-bs-toggle="offcanvas"
                                data-bs-target="#cartOffcanvas"
                                aria-controls="cartOffcanvas"
                            >
                                <i className="fa-solid fa-bag-shopping me-2"></i>
                                <span className="fw-bold">{cartItemCount}</span>
                            </button>
                        </div>
                    </div>
                </nav>

                {/*  Categorias Desktop */}
                <nav className="d-none d-lg-block border-top border-bottom py-2 bg-warning">
                    <div className="container-fluid d-flex justify-content-center">
                        <ul className="navbar-nav d-flex flex-row gap-5">
                            {CATEGORIAS_PRINCIPAIS.map((cat, index) => (
                                <li className="nav-item" key={index}>
                                    <a
                                        onClick={() => handleOpenProds(cat.id)}
                                        role="button"
                                        className={`nav-link text-uppercase fw-medium p-0 ${
                                            cat.destaque ? "text-danger" : "text-dark"
                                        }`}
                                        style={{ fontSize: "0.9rem", letterSpacing: "0.5px" }}
                                    >
                                        {cat.nome}
                                    </a>
                                </li>
                            ))}
                        </ul>
                    </div>
                </nav>
            </header>

            {/*  Offcanvas Mobile Menu */}
            <div
                className="offcanvas offcanvas-start"
                tabIndex="-1"
                id="mobileMenu"
                aria-labelledby="mobileMenuLabel"
            >
                <div className="offcanvas-header border-bottom">
                    <h5 id="mobileMenuLabel" className="fw-bold text-uppercase">
                        Menu
                    </h5>
                    <button
                        type="button"
                        className="btn-close text-reset"
                        data-bs-dismiss="offcanvas"
                        aria-label="Fechar"
                    ></button>
                </div>
                <div className="offcanvas-body">
                    <ul className="navbar-nav">
                        {NAV_LINKS.map((item) => (
                            <li className="nav-item mb-3" key={item.link}>
                                <Link
                                    to={item.link}
                                    className="nav-link text-dark fw-medium"
                                >
                                    {item.name}
                                </Link>
                            </li>
                        ))}
                    </ul>
                    <hr />
                    <h6 className="fw-bold mt-3 mb-2 text-uppercase">Categorias</h6>
                    <ul className="list-unstyled">
                        {CATEGORIAS_PRINCIPAIS.map((cat, index) => (
                            <li key={index} className="mb-2">
                                <a
                                    onClick={() => handleOpenProds(cat.id)}
                                    className={`text-decoration-none ${
                                        cat.destaque ? "text-danger fw-bold" : "text-dark"
                                    }`}
                                    data-bs-dismiss="offcanvas"
                                >
                                    {cat.nome}
                                </a>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>

            {/*  Offcanvas Carrinho (Lgica de exibio atualizada) */}
            <div
                className="offcanvas offcanvas-end"
                tabIndex="-1"
                id="cartOffcanvas"
                aria-labelledby="cartOffcanvasLabel"
            >
                <div className="offcanvas-header border-bottom">
                    <h5 id="cartOffcanvasLabel" className="fw-bold text-uppercase">
                        Seu Carrinho ({cartItemCount})
                    </h5>
                    <button
                        type="button"
                        className="btn-close text-reset"
                        data-bs-dismiss="offcanvas"
                        aria-label="Fechar"
                    ></button>
                </div>
                <div className="offcanvas-body d-flex flex-column">
                    <div className="flex-grow-1">
                        {!user?.id ? (
                            //  Usurio No Logado
                            <div className="text-center py-5">
                                <i className="fa-solid fa-lock fa-3x text-muted mb-3"></i>
                                <p className="fw-semibold">Inicie a Sesso para ver seu Carrinho</p>
                                <p className="small text-muted">Faa login para ver, adicionar e finalizar seus pedidos.</p>
                                <Link 
                                    to="/auth/login" 
                                    className="btn btn-outline-danger btn-sm mt-2"
                                >
                                    Fazer Login
                                </Link>
                            </div>
                        ) : loadingCart ? (
                            //  Carregando
                            <CartLoader />
                        ) : cartItems.length === 0 ? (
                            <div className="text-center py-5">
                                <i className="fa-solid fa-box-open fa-3x text-muted mb-3"></i>
                                <p className="fw-semibold">Seu carrinho est vazio.</p>
                                <p className="small text-muted">Explore nossos produtos e adicione itens para comear a sua compra!</p>
                            </div>
                        ) : (
                            <div>
                                {cartItems.map(item => (
                                    <div key={item.id} className="d-flex align-items-center justify-content-between mb-3 border-bottom pb-2">
                                        <div className="flex-grow-1 me-3">
                                            <h6 className="mb-1 text-truncate">{item.produto_nome}</h6>
                                            <small className="text-muted">
                                                {item.qtd}x - EUR {numberFormat(item.preco, 2, ',', '.')}
                                            </small>
                                        </div>
                                        <img
                                            src={`${url}/uploads/${item.foto}`}
                                            alt={item.nome}
                                            className="rounded"
                                            style={{ width: '60px', height: '60px', objectFit: 'cover' }}
                                        />
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                    
                    {/* Rodap do Carrinho (Exibido apenas se houver itens e o usurio estiver logado) */}
                    {user?.id && cartItems.length > 0 && (
                        <div className="border-top pt-3 flex-shrink-0">
                            <div className="d-flex justify-content-between mb-3">
                                <span className="fw-bold">Total:</span>
                                <span className="fw-bold text-danger">
                                    EUR {numberFormat(totalCartPrice, 2, ',', '.')}
                                </span>
                            </div>
                            <Link to = "/encomendas/finalizar" className="btn btn-dark w-100 rounded-pill py-2">
                                Finalizar Compra
                                <i className="fa-solid fa-arrow-right ms-2"></i>
                            </Link>
                        </div>
                    )}
                </div>
            </div>

            {/*  Pesquisa Overlay */}
            {isSearchOpen && (
                <div
                    className="position-fixed top-0 start-0 w-100 h-100 d-flex align-items-center justify-content-center animate__animated animate__fadeIn animate__faster"
                    style={{ backgroundColor: "rgba(0,0,0,0.95)", zIndex: 2000 }}
                >
                    <form
                        onSubmit={handleSearchSubmit}
                        className="d-flex align-items-center bg-white rounded-pill px-4 py-3 shadow-lg animate__animated animate__zoomIn"
                        style={{ width: "80%", maxWidth: "600px" }}
                    >
                        <i className="fa-solid fa-magnifying-glass me-3 text-muted fs-5"></i>
                        <input
                            type="text"
                            value={keyword}
                            onChange={(e) => setKeyword(e.target.value)}
                            className="border-0 bg-transparent fs-5 w-100"
                            placeholder="Palavra-Chave do Produto..."
                            style={{ border: "none", outline: "none" }}
                        />
                        <button
                            type="button"
                            className="btn text-dark"
                            onClick={() => setIsSearchOpen(false)}
                        >
                            <i className="fa-solid fa-xmark fs-4"></i>
                        </button>
                    </form>
                </div>
            )}
        </>
    );
};

export default Navbar;