
import { loader } from '../static';
import './Loader.css'
const Loader = () => {
 return (
  <div className="loader-wrapper text-center bg-dark"> 
    <div className="loader vh-100 d-flex align-items-center justify-content-center">
            <img
              src={loader}
              style={{
                width:70
              }}
            />
        </div>
  </div>
 );
};

export default Loader;