import React, { useEffect, useState } from "react";
import "animate.css";
import { hero1 } from "../static";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { numberFormat } from "../config/Config";
import { Link, useNavigate } from "react-router-dom";
import Title from "../components/Title";

const Index = () => {
  const [novos, setNovos] = useState([]);
  const [promocoes, setPromocoes] = useState([]);
  const [loadingNovos, setLoadingNovos] = useState(true);
  const [loadingPromocoes, setLoadingPromocoes] = useState(true);
  const navigate = useNavigate();

  const getNovos = async () => {
    setLoadingNovos(true);
    try {
      const res = await api.get(`/Produto.php?action=listByLimit&limit=8`);
      setNovos(res.data.data || []);
    } catch (error) {
      console.error("Erro ao buscar novos produtos:", error);
      setNovos([]);
    } finally {
      setLoadingNovos(false);
    }
  };

  const getPromocoes = async () => {
    setLoadingPromocoes(true);
    try {
      const res = await api.get(`/Promocao.php?action=listAll`);
      setPromocoes(res.data.data || []);
    } catch (error) {
      console.error("Erro ao buscar as promoes:", error);
      setPromocoes([]);
    } finally {
      setLoadingPromocoes(false);
    }
  };

  useEffect(() => {
    getNovos();
    getPromocoes();
  }, []);

  const handleOpenDetails = (id) => {
    navigate(`/produtos/detalhes/${id}/`);
  };

  const handleOpenPromoDetails = (id) => {
    navigate(`/promocoes/detalhes/${id}/`);
  };

  const calculatePrice = (originalPrice, discount) => {
    const finalPrice = originalPrice * (1 - discount / 100);
    return numberFormat(finalPrice, 2, ",", ".");
  };

  const Loader = () => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status">
        <span className="visually-hidden">A carregar...</span>
      </div>
      <p className="mt-3 text-muted">A carregar produtos...</p>
    </div>
  );

  return (
    <>
    <Title title="Pgina Inicial" />
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      {/* HEADER */}
      <Navbar />

      {/* CONTEDO SCROLLVEL */}
      <main
        className="flex-grow-1 w-100"
        style={{ overflowY: "auto", backgroundColor: "#fff" }}
      >
        {/*  PRODUTOS NOVOS */}
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Acabou de Chegar
          </h3>
          <div className="row">
            {loadingNovos ? (
              <div className="col-12">
                <Loader />
              </div>
            ) : novos.length > 0 ? (
              novos.map((item) => (
                <div
                  onClick={() => handleOpenDetails(item.id)}
                  key={item.id}
                  className="col-md-3 position-relative mb-5"
                  role="button"
                >
                  <img
                    src={url + "/uploads/" + item.foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "18em" }}
                    alt={item.nome}
                  />
                  <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                    {item.nome}
                  </h5>
                  <b className="text-dark fs-5">
                    EUR {numberFormat(item.preco, 2, ",", ".")}
                  </b>
                  <div
                    className="position-absolute d-flex align-items-center"
                    style={{
                      top: "16em",
                      left: 20,
                    }}
                  >
                    <Link
                      to={`/produtos/categoria/${item.categoria_id}`}
                      style={{
                        background: "rgba(0,0,0,0.6)",
                        backdropFilter: "blur(3px)",
                        textDecoration: "none",
                      }}
                      className="fw-bold p-1 px-2 rounded-4 text-white small animate__animated animate__fadeInUp"
                    >
                      {item.categoria_nome}
                    </Link>
                  </div>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-box-open fa-3x mb-3"></i>
                <p>Nenhum produto novo encontrado no momento.</p>
              </div>
            )}
          </div>

          {/*  BOTO VER MAIS (PRODUTOS) */}
          {novos.length > 0 && (
            <div className="text-center mt-4">
              <Link
                to="/produtos/"
                className="btn btn-outline-danger px-4 py-2 fw-semibold rounded-5"
              >
                Ver Mais...
              </Link>
            </div>
          )}
        </section>

        {/*  HERO PARALLAX */}
        <div
          className="d-flex align-items-center justify-content-center w-100"
          style={{
            height: "15em",
            backgroundImage: `url(${hero1})`,
            backgroundPosition: "center",
            backgroundSize: "cover",
            backgroundAttachment: "fixed",
            transition: "background-position 0.5s ease",
          }}
        ></div>

        {/*  PROMOES */}
        <section className="p-4 pt-5 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Promoes e Descontos
          </h3>
          <div className="row">
            {loadingPromocoes ? (
              <div className="col-12">
                <Loader />
              </div>
            ) : promocoes.length > 0 ? (
              promocoes.map((promo) => (
                <div
                  key={promo.id}
                  onClick={() => handleOpenPromoDetails(promo.id)}
                  role="button"
                  className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5"
                >
                  <img
                    src={url + "/uploads/" + promo.produto_foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "18em" }}
                    alt={promo.produto_nome}
                  />

                  <div className="position-absolute top-0 end-0 bg-danger text-white rounded-bottom-left-3 py-1 px-3 fw-bold small shadow-sm">
                    -{numberFormat(promo.desconto, 0, ",", ".")} %
                  </div>

                  <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                    {promo.produto_nome}
                  </h5>

                  <div className="d-flex align-items-center">
                    <span className="text-muted text-decoration-line-through small me-2">
                      EUR {numberFormat(promo.produto_preco || 50, 2, ",", ".")}
                    </span>

                    <b className="text-dark fs-5">
                      EUR {calculatePrice(promo.produto_preco, promo.desconto)}
                    </b>
                  </div>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-tag fa-3x mb-3"></i>
                <p>Nenhuma promoo ativa no momento. Fique de olho!</p>
              </div>
            )}
          </div>

          {/*  BOTO VER MAIS (PROMOES) */}
          {promocoes.length > 0 && (
            <div className="text-center mt-4">
              <Link
                to="/promocoes/"
                className="btn btn-outline-danger px-4 py-2 fw-semibold rounded-5"
              >
                Ver Mais...
              </Link>
            </div>
          )}
        </section>

        {/*  FOOTER */}
        <Footer />
      </main>
    </div>
    </>
  );
};

export default Index;