import { useEffect, useState } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api"; 
import { Link } from "react-router-dom";
import Title from "../components/Title";


const Categorias = () => {
    const [categorias, setCategorias] = useState([]);
    const [loadingCategorias, setLoadingCategorias] = useState(true);

    // Funo para buscar todas as categorias
    const getCategorias = async () => {
        setLoadingCategorias(true);
        try {
            const res = await api.get(`/Categoria.php?action=listAll`);
            setCategorias(res.data.data || []); 
        } catch (error) {
            console.error("Erro ao buscar categorias:", error);
            setCategorias([]);
        } finally {
            setLoadingCategorias(false);
        }
    };

    useEffect(() => {
        getCategorias();
    }, []); 

    // Componente de Loader simples
    const Loader = () => (
        <div className="text-center py-5">
            <div className="spinner-border text-primary" role="status">
                <span className="visually-hidden">A carregar...</span>
            </div>
            <p className="mt-3 text-muted">A carregar categorias...</p>
        </div>
    );

    // Renderizao do Card de Categoria
    const CategoryCard = ({ id, nome, foto, descricao }) => (
        <Link 
            to={`/produtos/categoria/${id}`} 
            className="col-12 col-sm-6 col-md-4 col-lg-3 position-relative mb-5 text-decoration-none text-dark"
        >
            <div className="card shadow-sm h-100 rounded-3 border-0 animate__animated animate__fadeInUp">
                <img
                    src = {url+"/uploads/"+foto}
                    className="w-100 object-fit-cover rounded-top-3"
                    style = {{ height:"12em" }}
                    alt={`Categoria: ${nome}`}
                />
                <div className="card-body p-3 text-center">
                    <h5 className="mb-0 fw-bold text-truncate">{nome}</h5>
                    <p className="text-muted small mt-1">{descricao.substring(0, 50)}{descricao.length > 50 ? '...' : ''}</p>
                </div>
            </div>
        </Link>
    );

    return (
        <div
            className="d-flex flex-column"
            style={{
                position: "fixed",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: "#f4f4f4",
            }}
        >
             <Title title="Categorias" />
            {/* HEADER */}
            <Navbar/>

            {/* CONTEDO SCROLLVEL */}
            <main
                className="flex-grow-1 w-100"
                style={{ overflowY: "auto", backgroundColor: "#fff" }}
            >
                
                {/* LISTAGEM DE CATEGORIAS */}
                <section className="p-4 pt-5 pb-5 container-fluid">
                    <h3 className="text-dark mb-5 fw-bolder text-center border-bottom pb-3"> Explorar Todas as Categorias</h3>
                    <div className="row justify-content-start">
                        {
                            loadingCategorias ? (
                                <div className="col-12"><Loader /></div>
                            ) : categorias.length > 0 ? (
                                categorias.map(item => (
                                    <CategoryCard 
                                        key={item.id}
                                        id={item.id}
                                        nome={item.nome}
                                        foto={item.foto}
                                        descricao={item.descricao}
                                    />
                                ))
                            ) : (
                                <div className="col-12 text-center text-muted py-5">
                                    <i className="fas fa-folder-open fa-3x mb-3"></i>
                                    <p>Nenhuma categoria encontrada no momento.</p>
                                </div>
                            )
                        }
                    </div>
                </section>

                <Footer/>
            </main>
        </div>
    );
};

export default Categorias;