import React, { useEffect, useState, useRef, useCallback } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { numberFormat } from "../config/Config";
import { Link, useNavigate } from "react-router-dom";
import Title from "../components/Title";

const ProdutosAll = () => {
  const [produtos, setProdutos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [limit] = useState(8);
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const navigate = useNavigate();

  // referncia ao main (container scrollvel)
  const scrollContainerRef = useRef(null);

  // busca produtos do backend
  const fetchProdutos = useCallback(
    async (isLoadMore = false) => {
      if (isLoadMore) setLoadingMore(true);
      else setLoading(true);

      try {
        const currentOffset = isLoadMore ? offset : 0;

        const res = await api.get(
          `/Produto.php?action=listByLimit&limit=${limit}&offset=${currentOffset}`
        );

        const data = res?.data?.data || [];

        if (data.length === 0) {
          setHasMore(false);
          return;
        }

        if (isLoadMore) {
          setProdutos((prev) => [...prev, ...data]);
          setOffset((prev) => prev + limit);
        } else {
          setProdutos(data);
          setOffset(limit);
        }

        if (data.length < limit) setHasMore(false);
      } catch (error) {
        console.error("Erro ao buscar produtos:", error);
      } finally {
        if (isLoadMore) setLoadingMore(false);
        else setLoading(false);
      }
    },
    [limit, offset]
  );

  useEffect(() => {
    fetchProdutos(false);
  }, []);

  // scroll infinito dentro do MAIN
  useEffect(() => {
    const container = scrollContainerRef.current;
    if (!container) return;

    const handleScroll = () => {
      const nearBottom =
        container.scrollTop + container.clientHeight >=
        container.scrollHeight - 100;

      if (nearBottom && hasMore && !loadingMore) {
        fetchProdutos(true);
      }
    };

    container.addEventListener("scroll", handleScroll);
    return () => container.removeEventListener("scroll", handleScroll);
  }, [fetchProdutos, hasMore, loadingMore]);

  const handleOpenDetails = (id) => navigate(`/produtos/detalhes/${id}/`);

  const Loader = ({ text = "A carregar produtos..." }) => (
    <div className="text-center py-5">
      <div className="spinner-border text-danger" role="status"></div>
      <p className="mt-3 text-muted">{text}</p>
    </div>
  );

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Title title="Explorar Produtos" />
      <Navbar />

      {/* SCROLL DENTRO DO MAIN */}
      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Itens disponveis
          </h3>

          <div className="row">
            {loading ? (
              <Loader />
            ) : produtos.length > 0 ? (
              produtos.map((item) => (
                <div
                  onClick={() => handleOpenDetails(item.id)}
                  key={item.id}
                  className="col-md-3 position-relative mb-5"
                  role="button"
                >
                  <img
                    src={url + "/uploads/" + item.foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "18em" }}
                    alt={item.nome}
                    onError={(e) => (e.target.src = url + "/uploads/no-image.jpg")}
                  />
                  <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                    {item.nome}
                  </h5>
                  <b className="text-dark fs-5">
                    EUR {numberFormat(item.preco, 2, ",", ".")}
                  </b>
                  <div
                    className="position-absolute d-flex align-items-center"
                    style={{ top: "16em", left: 20 }}
                  >
                    <Link
                      to={`/produtos/categoria/${item.categoria_id}`}
                      className="fw-bold p-1 px-2 rounded-4 text-white small animate__animated animate__fadeInUp"
                      style={{
                        background: "rgba(0,0,0,0.6)",
                        backdropFilter: "blur(3px)",
                        textDecoration: "none",
                      }}
                    >
                      {item.categoria_nome}
                    </Link>
                  </div>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-box-open fa-3x mb-3"></i>
                <p>Nenhum produto encontrado.</p>
              </div>
            )}
          </div>

          {loadingMore && <Loader text="Carregando mais produtos..." />}

          {!hasMore && produtos.length > 0 && (
            <p className="text-center text-muted mt-4">
               Todos os produtos foram carregados.
            </p>
          )}
        </section>

        <Footer />
      </main>
    </div>
  );
};

export default ProdutosAll;
