import React, { useEffect, useState, useRef, useCallback } from "react";
import "animate.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { api, url } from "../axios/api";
import { numberFormat } from "../config/Config";
import { Link, useNavigate, useLocation } from "react-router-dom"; 

const ProdutosPesquisa = () => {
  // Extrair o termo de pesquisa da QUERY STRING (?q=...)
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const termo = queryParams.get('q'); // Obtm o valor do parmetro 'q'

  const [produtos, setProdutos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [limit] = useState(8); // O limite ainda  usado na chamada da API, mas sem paginao
  const navigate = useNavigate();

  // A ref  mantida para ser usada no main/scrollContainer, mas no ser usada para o infinite scroll
  const scrollContainerRef = useRef(null);

  //  Funo para buscar produtos filtrados (Apenas a primeira pgina)
  const fetchProdutos = useCallback(
    async () => {
      if (!termo) return;
      if (loading) return; // Clusula de guarda simples

      setLoading(true);

      try {
        // Chamada da API sem offset, apenas o limite
        const res = await api.get(
          `/Produto.php?action=search&keyword=${encodeURIComponent(termo)}&limit=${limit}&offset=0` 
        );

        const data = res?.data?.data || [];

        setProdutos(data);

      } catch (error) {
        console.error("Erro ao buscar produtos filtrados:", error);
        setProdutos([]);
      } finally {
        setLoading(false);
      }
    },
    [termo, limit, loading]
  );


  //  Efeito para resetar e iniciar a busca quando o termo mudar (ou na primeira montagem)
  useEffect(() => {
    // Resetar o estado
    setProdutos([]);
    
    // Explicitamente configuramos o loading para true antes da primeira busca, se houver termo.
    if (termo) {
        setLoading(true);
        //  CHAMA A BUSCA
        fetchProdutos();
    } else {
        // Se o termo estiver vazio (ex: /produtos/search/?q=)
        setLoading(false);
    }
  }, [termo]); // fetchProdutos  removido das dependncias para evitar loops, j que ele depende de 'loading'

  // A funo fetchProdutos no est mais sendo usada como uma dependncia no useEffect abaixo.
  // Em vez disso, a chamada  feita diretamente na montagem do componente.
  // Para evitar warnings, vamos ajustar o useCallback, removendo 'loading' e 'loadingMore' da lista de dependncias.
  const stableFetchProdutos = useCallback(
    async () => {
      if (!termo) return;

      setLoading(true);

      try {
        const res = await api.get(
          `/Produto.php?action=search&keyword=${encodeURIComponent(termo)}&limit=${limit}&offset=0` 
        );

        const data = res?.data?.data || [];
        setProdutos(data);

      } catch (error) {
        console.error("Erro ao buscar produtos filtrados:", error);
        setProdutos([]);
      } finally {
        setLoading(false);
      }
    },
    [termo, limit] // Dependncias mnimas
  );

  useEffect(() => {
    setProdutos([]);
    if (termo) {
        setLoading(true);
        stableFetchProdutos();
    } else {
        setLoading(false);
    }
  }, [termo, stableFetchProdutos]); // Usamos a verso estvel aqui

  //  REMOVIDO: useEffect para Scroll infinito

  const handleOpenDetails = (id) => navigate(`/produtos/detalhes/${id}/`);

  const Loader = ({ text = "A carregar produtos..." }) => ( 
    <div className="text-center py-5"> 
      <div className="spinner-border text-danger" role="status"></div> 
      <p className="mt-3 text-muted">{text}</p> 
    </div>
  );

  return (
    <div
      className="d-flex flex-column"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "#f4f4f4",
      }}
    >
      <Navbar />

      <main
        ref={scrollContainerRef}
        className="flex-grow-1 w-100"
        style={{
          overflowY: "auto",
          backgroundColor: "#fff",
          height: "100vh",
        }}
      >
        <section className="p-4 pt-4 pb-5">
          <h3 className="text-dark mb-4 fw-bold border-bottom pb-2">
             Resultados para: <span className="text-danger">{termo || '...'}</span>
          </h3>

          <div className="row">
            {loading ? (
              <Loader />
            ) : produtos.length > 0 ? (
              produtos.map((item) => (
                <div
                  onClick={() => handleOpenDetails(item.id)}
                  key={item.id}
                  className="col-md-3 position-relative mb-5"
                  role="button"
                >
                  <img
                    src={url + "/uploads/" + item.foto}
                    className="w-100 object-fit-cover rounded-3 shadow-sm"
                    style={{ height: "18em" }}
                    alt={item.nome}
                    onError={(e) =>
                      (e.target.src = url + "/uploads/no-image.jpg")
                    }
                  />
                  <h5 className="mb-0 mt-2 fw-semibold text-truncate">
                    {item.nome}
                  </h5>
                  <b className="text-dark fs-5">
                    EUR {numberFormat(item.preco, 2, ",", ".")}
                  </b>
                  <div
                    className="position-absolute d-flex align-items-center"
                    style={{ top: "16em", left: 20 }}
                  >
                    <Link
                      to={`/produtos/categoria/${item.categoria_id}`}
                      className="fw-bold p-1 px-2 rounded-4 text-white small animate__animated animate__fadeInUp"
                      style={{
                        background: "rgba(0,0,0,0.6)",
                        backdropFilter: "blur(3px)",
                        textDecoration: "none",
                      }}
                    >
                      {item.categoria_nome}
                    </Link>
                  </div>
                </div>
              ))
            ) : (
              <div className="col-12 text-center text-muted py-5">
                <i className="fas fa-box-open fa-3x mb-3"></i>
                <p>Nenhum item encontrado para "{termo}".</p>
              </div>
            )}
          </div>

        </section>

        <Footer />
      </main>
    </div>
  );
};

export default ProdutosPesquisa;